<template>
    <main><section v-for="(tag, i) in tags" :key="tag+i">
        <div class="recipes-section gutter flex-spread">
            <h2 class="">{{tag}}</h2>
            <!-- <h2>{{recipes.filter(rec=> rec.tags.indexOf(tag) != -1).length}} {{recipes.filter(rec=> rec.tags.indexOf(tag) != -1).length == 1?'recipe':'recipes'}}</h2> -->
        </div>
        <div class="recipes-vertical-track gutter-lr">
            <div class="recipes-card" @click="()=>$emit('handleSelection', recipe.id)"
            :class="{'recipes-card-left':i%2===0,'recipes-card-right': i%2!=0,'gradient': viewVersion === 3}"
            v-for="(recipe, i) in recipes.filter(rec=> rec.tags.indexOf(tag) != -1)" :key="recipe.id" 
                :style="!!recipe.images ?`background-image: url(${require('@/assets/recipes/'+getImage(recipe.id))})`:''"
            >
                <div class="recipes-card-info">
                    <h3 v-if="viewVersion === 3">{{recipe.name}}</h3>
                    <div class="flex-start">
                        <!-- info icon -->
                        <div v-if="!!recipe.text[1]" class="recipes-card-icon flex">
                            <i class="bcon-bars"></i>
                        </div> 
                        <!-- video icon -->
                        <div v-if="!!recipe.videos.length" class="recipes-card-icon flex">
                            <i class="fas fa-video"></i>
                        </div> 
                        <!-- ingredient icon -->
                        <div v-if="parseInt(recipe.ingredients)" class="recipes-card-icon flex">
                            <i class="fas fa-apple-alt"></i>
                        </div> 
                    </div>
                </div>
            </div>
        </div>
    </section></main>
</template>

<script>
export default {
    props:{recipes:Array, tags:Array, viewVersion:Number},
    methods:{
        getImage(id){
            return this.viewVersion === 2 ? this.recipes.filter(x=> {return x.id == id})[0].images[0] : this.recipes.filter(x=>{return x.id == id})[0].images[1];
        }
    }
}
</script>

<style>

</style>



