<template>
    <div class="flex" style="height: 100%; text-align: center;" @click="reload">
        <div>
        <h1>😳<br>Something went wrong</h1>
        <p class="recipes-empty-sub-text">Tap to Reload</p>
            <div class="flex">
                <i class="bcon-home" style="font-size: 15vw"></i>
            </div>    
        </div>
    </div>
</template>

<script>
export default {
    methods:{
        reload(){
            window.location.reload();
        }
    }
}
</script>

<style>

</style>



