import Moment from './dateFormatter'
import Store from '../store'
class Event {
    constructor(type,recipeId,event){
        this.date = Moment.getDateTime();
        this.machine_id = Store.getters.getMachineId ? Store.getters.getMachineId : null;
        this.ui_id = Store.getters.getUiId ? Store.getters.getUiId : null;
        this.store_id = null;
        // applied arguments
        this.recipe_id = recipeId ? recipeId : -1;
        this.event = event ? event : -1;
        this.type = type ? type : -1;
    }
}
const logger = {
    Log(obj){
        // obj = {type: 1, event: error-event-code, recipe_id: id}
        console.log("JSLOGGER: Logging new event: ", obj.type)
        let logEvent = new Event(obj.type,obj.recipe_id,obj.event)
        console.log("new event: ", logEvent)
        logEvent.meta = JSON.stringify(obj.meta);
        window.app.logEvent(JSON.stringify(logEvent))
    }
}

export default logger;

