<template>
    <div class="rec-modal-info gutter"  @click="()=>$emit('toggleDetails')">
        <p :class="{'rec-summary':true, 'rec-summary-hide':!showDetails}">
            {{recipe.text[0]}}
        </p>
        <div class="flex-spread rec-modal-buttons" :style="showDetails? '':'padding-top:0 !important;transition.2s'">
            <i :class="{'bcon-chevron-right rec-modal-chev':true, 'rec-modal-chev-turn':showDetails}"></i>
            <div class="flex-start" v-show="showDetails">
                <!-- info icon -->
                <button v-if="isStory" class="rec-modal-info-button flex" @click="(e)=>{e.stopPropagation(); $emit('launchStory');}">
                    <i class="bcon-bars"></i>
                </button> 
                <!-- video icon -->
                <button v-if="isVideo" class="rec-modal-info-button flex" @click="(e)=>{e.stopPropagation(); $emit('launchVideo');}">
                    <i class="fas fa-video"></i>
                </button> 
                <!-- ingredient icon -->
                <button v-if="isIngredient" class="rec-modal-info-button flex" @click="(e)=>{e.stopPropagation(); $emit('launchIngredient');}">
                    <i class="fas fa-apple-alt"></i>
                </button> 
            </div>
            <button v-if="showDetails" class="rec-modal-info-button-details flex">
                <span style="opacity: .3;">show less</span>
            </button> 
                <h3 v-else class="rec-modal-title">Recipe Details </h3>
        </div>
    </div>
</template>

<script>
export default {
props:{recipe:Object,showDetails:Boolean, isVideo:Boolean, isStory:Boolean, isIngredient:Boolean}
}
</script>



