//calculates the estimated run time of a recipe
const getRuntime = (steps)=>{
    let runtime = 0;
    let runArr = []
    steps.forEach(step => {
        switch (true){
            case (step.type.toLowerCase().indexOf('st') != -1):
            runtime += 8;
            runArr.push(8)
            break
    
            //Fill
            case (step.type.toLowerCase() == 'fr'):
                const pause = step.values.ap ? parseInt(step.values.ap) : 0;
                const fill = step.values.fwv ? Math.round(parseInt(step.values.fwv)/17) : 0;
                const rinse = step.values.rwv ? Math.round(parseInt(step.values.rwv)/17) : 0;
                // runtime += (fill + pause + rinse);
                runArr.push(fill+rinse + pause);
                break   
    
            //Vacuum
            case (step.type.toLowerCase() == 'vc'):
                let hold = step.values.tm ? parseInt(step.values.tm) : 0;
                let vpause = step.values.ap ? parseInt(step.values.ap) : 0;
                let vc = step.values.v ? parseInt(step.values.v) : 0;
                // runtime += (hold + vpause);
                runArr.push(hold + vpause + vc);
                break       
    
            //Purge
            //if no manstop divide time/2
            case (step.type.toLowerCase() == 'pg'):
                    runArr.push(10)
                break 

            // Dialog
            case (step.type.toLowerCase() == 'dialog'):
                // runtime += 1;
                runArr.push(1)
                break 

            case (step.type.toLowerCase() == 'bo'):
                // runtime += 1;
                runArr.push(2)
                break

            //NEW STEPS
            case (step.type.toLowerCase() == 'fi'):
                const fi = step.values.v ? parseInt(step.values.v)/17 : 0;
                runArr.push(Math.round(fi));
                break  
            
            case (step.type.toLowerCase() == 'ri'):
                const ri = step.values.v ? parseInt(step.values.v)/17 : 0;
                runArr.push(Math.round(ri));
                break
            
            case (step.type.toLowerCase() == 'rf'):
                const rf = step.values.v ? parseInt(step.values.v)/17 : 0;
                runArr.push(Math.round(rf));
                break

            case (step.type.toLowerCase() == 'sp'):
                const sp = step.values.v ? parseInt(step.values.v) : 0;
                runArr.push(sp);
                break

            default:
                // console.log('Couldn\'t convert step to time: ', step)   
        }
    });
    return runArr.length > 0 ? runArr : [0];
}

export default getRuntime


// time is time: vacuum atm pause
//water is math.round(volume / 17)
//prep is 3 - 15 seconds. usually 7 - 8 secs


