const extractor = (recipesArr)=>{
    let cTags = [];
    recipesArr.forEach(rec => {
        rec.tags.forEach(tag => {
            if(
                tag.toLowerCase().indexOf('service') === -1 &&
                tag.toLowerCase().indexOf('maintenance') === -1 &&
                tag.indexOf('$') != -1
            ){
                cTags.push(tag);
            }
        })
    });
    return cTags.sort((a, b)=>{
        if(a.toLowerCase() < b.toLowerCase()) return -1;
        if(a.toLowerCase() > b.toLowerCase()) return 1;
        return 0;
    });
}

export default extractor;


