<template>
    <div class="panel panel-right" :class="{'panel-right__opened':isOpened, 'iphonex-filter-panel':isiPhoneX}">
        <div id="filter-panel-swipe" 
            :class="{'filter-swipe-container':true, 'filter-tags-show':false}" 
            :style="'transform:translate3d('+position+'px,0,0);'"
        >

            <h3 @click="selectView(1)" :style="currentView === 1 ? 'font-size: 4vw': ''">view 1</h3>
            <h3 @click="selectView(2)" :style="currentView === 2 ? 'font-size: 4vw': ''">view 2</h3>
            <h3 @click="selectView(3)" :style="currentView === 3 ? 'font-size: 4vw': ''">view 3</h3>
            <!-- <collection-filter  v-if="isOpened"
                :isOpened="isOpened" 
                :selectedCollection="selectedCollection" 
                :dragDistance="position"
                @selectFilter="selectCollection($event)"
                @updatePosition="updatePosition($event)"
                @switchView="showCollections = !showCollections"
            />
            <tag-filter v-if="isOpened"
                :isOpened="isOpened" 
                :selectedFilters="selectedFilters" 
                :tags="tags"
                :dragDistance="position"
                @selectFilter="selectTag($event)"
                @updatePosition="updatePosition($event)"
                @switchView="showCollections = !showCollections"
            /> -->
        </div>
        <div class="filter-panel-dot-wrapper flex">
            <div :class="{'filter-panel-dot':true,'filter-panel-dot__active': position === 0}"></div>
            <div :class="{'filter-panel-dot':true,'filter-panel-dot__active': position === -350 }"></div>
        </div>
    </div>
</template>

<script>
import CollectionFilter from './filterpanel/CollectionFilter.vue'
import TagFilter from './filterpanel/TagFilter.vue'
import AppUpdate from '../scripts/eventBus.js'
import Store from '../store/'
export default {
    components:{CollectionFilter, TagFilter},
    props:{isOpened: Boolean, tags:Array, currentView:Number},
    computed:{
        selectedCollection(){
            return Store.getters.getCurrentCollection;
        },
        selectedFilters(){
            return Store.getters.getCurrentFilters;
        },
        isiPhoneX(){
           return Store.getters.isiPhoneX;
       }
    },
    methods:{
        selectView(v){
            this.$emit('switchView', v);
        },
        updatePosition(pos){
            this.position = pos;
        },
        filterClass(type){
            if(this.selectedFilters.indexOf(type) != -1){
                return 'filter-chip__active filter-chip__active-' + type;
            }else{
                return 'filter-chip__' + type;
            }
        },
        selectCollection(col){
            console.log(col)
            const update = this.selectedCollection == col ? '' : col;
            Store.commit('setCurrentCollection', update)
        },
        selectTag(filter){
            Store.commit('setCurrentFilters', filter)
        }
    },
    data(){
        return{
            showCollections: true,
            position: 0,
            typeFilters: ['tea','coffee','spirit','infusion','other']
        }
    }
}
</script>



