<template>
    <div :class="{'run-bg':true,'run-completed':true, 'flex':isFinished,'run-error':isError && errorCode.title != 'Test Output'}">
        <!-- Brewing display -->
        <div :class="{'run-brewing':true,'run-sm-center':true, 'run-brewing__done':isFinished}">
          <div class="run-top-bar"></div>
          <!-- Manual Cycle display -->
          <div v-if="manualCycle">
            <div class="run-title-wrapper" style="padding: 4vw">
              <div class="run-manual-cog-wrapper">
                <i class="bcon-cog run-manual-cog cog-spin"></i>
              </div>
              <p class="run-title" :class="{'run-title-sm': recipe.name.length > 20}" style="padding-top: 0">{{recipe.name}}</p>
              <p class="run-title-sub">{{decodeStep(steps[currentStep])}}</p>
            </div>
            <div class="run-manual-wrapper flex">
              <Brewer :size="50" :curStep="steps[currentStep]" :steps="steps"/>
            </div>
          </div>
          <!-- Regular recipe view -->
          <div v-else class="flex-spread">
            <!-- Left Side -->
            <section class="run-view-half gutter-right gutter-bottom"> 
              <Chamber :step="steps[currentStep]"/> 
            </section>
            <!-- Right Side -->
            <section class="run-view-half gutter-right gutter-bottom">
              <div class="run-title-wrapper">
                <p class="run-title">{{decodeURIComponent($route.params.name)}}</p>
                <p class="run-title-sub">{{decodeURIComponent($route.params.size)}}</p>
              </div>
              <!-- Talking points recipe.text-->
              <div style="width: 100%; height: 40%; display:block;">
                <div class="rec-modal-divider"></div>
                <talking-points :talkingPoints="recipe.text.slice(2)" />
                <div class="rec-modal-divider"></div>
              </div>

              <div>
                <p class="run-service-time" @click="()=>currentStep === steps.length-1? finishRecipe() : nextStep()">
                  {{displayTime}}
                </p>
              </div>
                <!-- Recipe Display -->
                <!-- <div class="run-progress"></div> -->
            </section>
          </div>
            <!-- Abort -->
            <section>
              <run-step-display :steps="steps" :currentStep="currentStep"/>
              <div :class="{'abort':true, 'flex':true,'abort-opened':abortOpened}" @click="toggleAbort">
                  <i v-if="!abortOpened" class="bcon-times abort-icon"></i>
                  <p v-else style="margin:0;">ABORT</p>
              </div>
            </section>
            <div :class="{'abort-bg':true, 'abort-bg-opened':abortOpened}" @click="closeAbort"></div>
        </div>
        <!-- Completed background -->
        <!-- Recipe Completed -->
        <div v-if="isFinished && !isError && !isServiceRec" style="text-align: center; height:100vh;" class="flex" @click="closeFinishedView">
            <div class="">
                <div class="flex" >
                    <!-- <img src="../assets/bkon-dots_white.png" class="run-completed-dots"> -->
                </div>
                <h3 class="run-completed-message">Enjoy!</h3>
            </div>
        </div>
        <!-- Error Message -->
        <div v-else-if="isFinished && isError && errorCode.title != 'Test Output'" style="text-align: center; height:100vh" class="flex" @click="closeFinishedView">
            <div class="">
                <h3 class="run-error-message">ERROR:</h3>
                <span class="run-error-title">{{errorCode.title}}</span>
                <p class="run-error__error-code">{{errorCode.text}}</p>
            </div>
            <div class="flex run-error__error-close" style="width: 100%;">
              <i class="bcon-times"></i>
            </div>
        </div>
        <!-- Service Result -->
        <div v-else-if="isFinished && isError && errorCode.title == 'Test Output'" style="text-align: center; height:100vh" class="flex" @click="closeFinishedView">
            <div class="">
                <span class="run-error-title">{{recipe.name}}</span>
                <p class="run-error__error-code">{{errorCode.title}}</p>
                <h3 class="run-error-message">{{errorCode.text}}</h3>
            </div>
            <div class="flex run-error__error-close" style="width: 100%;">
              <i class="bcon-times"></i>
            </div>
        </div>
        <!-- RESET BUTTON -->
        <div v-if="isFinished && isError && errorCode.isFatal" class="reset-button-wrapper flex">
          <div class="reset-button flex" @click="resetMachine">
            RESET
          </div>
        </div>

        <!-- Dialogs -->
        <Dialog :isOpened="dialogOpened" :type="dialogType" :dialogText="dialogText" @closeDialog="closeDialog($event)"/>
        <div :class="{'overlay-visible':dialogOpened, 'overlay':!dialogOpened}"></div>
    </div>
</template>

<script>
import Dialog from '../components/popups/Dialog.vue'
import TalkingPoints from '../components/TalkingPoints.vue'
// import ProgressBar from "../components/ProgressBarCircle.vue";
import RunStepDisplay from "../components/RunStepDisplay.vue"
import Chamber from "../components/Chamber.vue"
import DecodeStep from "../scripts/stepGlossary";
import Runtime from "../scripts/runtime";
import Moment from '../scripts/dateFormatter'
import appUpdate from "../scripts/eventBus";
import Logger from '../scripts/logger'
import convertRecForSbux from '../scripts/recipeConversion'
import Store from "../store";
export default {
  components: {  Dialog, TalkingPoints, Chamber, RunStepDisplay},
  created() {
    if(this.sendWholeRecipe){
      appUpdate.$on("stepCompleted", ($event) => {
        if(!this.dialogOpened){
          this.nextStep();
          }
      }),
        appUpdate.$on("recipeCompleted", $event => {
          if(this.manualCycle){
            this.$router.push("/");
          }else{
            this.finishRecipe();
          }
      }),
      appUpdate.$on("recipeError", $event => {
          this.isError = true;
          this.errorCode = {title:$event.title, text:$event.text, isFatal: $event.fatal ? $event.fatal  : false};
          clearInterval(this.timer);
          this.finishRecipe();
      }),
      appUpdate.$on("dialog", $event => {
          clearInterval(this.timer);
          this.dialogType = 'dialog'
          this.openDialog($event);
      }),
      appUpdate.$on("stepDialog", $event => {
          this.dialogType = 'step'
          this.openDialog($event);
      });
    }
  },
  mounted(){
      window.app.sendRecipe(JSON.stringify(
        this.steps
      ))
      this.getRuntime()
      this.startTimer(this.timeArr.reduce((x,y) =>{return x+y}))

  },
  beforeDestroy(){
    appUpdate.$off("stepCompleted");
    appUpdate.$off("recipeCompleted");
    appUpdate.$off("recipeError");
    appUpdate.$off("dialog");
    appUpdate.$off("stepDialog");
    Store.dispatch('killRecipe');
 },
  methods: {
    nextStep() {
      this.percentageTick = 0;
      if (this.currentStep < this.steps.length - 1) {
        this.currentStep += 1;
        this.startTimer(this.timeArr.slice(this.currentStep, 100).reduce((x,y) =>{return x+y}))
      }
      if (this.steps[this.currentStep].type.toLowerCase().indexOf('dialog') == -1) {
        this.dialogOpened = false;
        this.stepDialogOpened = false;
      }
      // remove after sbux demo
      if (this.steps[this.currentStep].type.toLowerCase().indexOf('dialog') != -1) {
        let $this = this
        setTimeout(clearInterval($this.timer),200);
        window.vue.$children[0].dataUpdate("dialog:"+decodeURIComponent(this.steps[this.currentStep].values.text));
      }
    },
    finishRecipe() {
      let self = this;
      this.isFinished = true;
      this.stepDialogOpened = false;
      this.dialogOpened = false;
      if (!this.isError) {
        clearInterval(this.timer);
        //checks if recipe completed is a Descale Cycle
        if(this.recipe.name.toLowerCase().indexOf('descale') !=-1 && (this.recipe.tags.indexOf('Maintenance') != -1||this.recipe.tags.indexOf('maintenance') != -1)){
          console.log("Descale cycle completed")
          window.app.updateDescaleCounter(JSON.stringify({"key":"descaleRequired","value":false}))
          window.app.updateDescaleCounter(JSON.stringify({"key":"lastDescaleDate","value":Moment.getDateTime()}))
          Logger.Log({type: this.currentRecipeMeta.type, recipe_id: this.currentRecipeMeta.id, event:200,
          meta:{"name":"Descale","size":"Maintenance", "action":"Completed"}})
        }
        this.completeTimeout = setTimeout(function() { 
          self.$router.push("/");
        }, 3000);
      }
    },
    closeAbort() {
      this.abortOpened = false;
    },
    closeFinishedView(){
      if(!this.errorCode.isFatal){
        clearTimeout(this.completeTimeout)
        if(this.isError){clearInterval(this.timer);}
        this.$router.push(this.isError ? this.goBack :"/");
      }
    },
    resetMachine(){
      clearTimeout(this.completeTimeout)
        if(this.isError){clearInterval(this.timer);}
        window.app.sendCommand('<RESET></RESET>')
        this.$router.push(this.isError ? this.goBack :"/");
    },
    openDialog: function(text) {
      //checks if stepCompleted came through from mainboard to maintain currentStep accuracy
      // if(!this.steps[this.currentStep].values.dialog && this.dialogType === 'dialog'){
        if(!this.steps[this.currentStep].values.text && this.dialogType === 'dialog'){
        this.nextStep();
      }
      this.dialogOpened = true;
      this.dialogText = text;
      if(this.isSoundOn){
        window.app.playSound('dialog')
      }
    },
    closeDialog(input){
        this.dialogOpened = false
        if(this.dialogType == 'dialog' && input == 1){
          //continue
          this.nextStep()//put in during demo
          window.app.dialogResponse(input);
          this.startTimer(this.timeLeft);
        }else if(this.dialogType == 'step' && input == 0){
          //manual stop
          window.app.dialogResponse(input);
          this.startTimer(this.timeLeft);
        }
        if(this.dialogType != 'step' && input == 0){
           window.app.dialogResponse(input)
            //abort
            clearInterval(this.timer);
            this.$router.push(this.goBack);
            Logger.Log({type: this.currentRecipeMeta.type, recipe_id: this.currentRecipeMeta.id, event:202,
              meta:{"name":encodeURIComponent(this.currentRecipeMeta.name),"size":encodeURIComponent(this.currentRecipeMeta.size), "action":"Aborted"}
            })
        }
    },
    toggleAbort() {
      if (this.abortOpened) {
        //Abort recipe
        clearTimeout(this.abortTimeout);
        clearInterval(this.timer);
        this.timer = null;
        window.app.abortRec();
        if(this.$route.params.size != "Maintenance"){
          window.app.updateCount('aborts')
        }
          Logger.Log({type: this.currentRecipeMeta.type, recipe_id: this.currentRecipeMeta.id, event:202,
          meta:{"name":encodeURIComponent(this.currentRecipeMeta.name),"size":encodeURIComponent(this.currentRecipeMeta.size), "action":"Aborted"}})
          Store.commit('setIsBrewing', false);
        this.$router.push(this.goBack);
      } else {
        let self = this;
        //check if timeout exist before setting to prevent overwriting making impossible to clear
        this.abortTimeout = setTimeout(function() {
          self.abortOpened = false;
        }, 5000);
      }
      this.abortOpened = !this.abortOpened;
    },
    decodeStep(step) {
      return DecodeStep(step);
    },
    startTimer(time) {
      var minutes, seconds;
      let timeRemaining = time;
      let self = this;
      
      this.timeLeft = time;
      this.totalTime = this.timeArr.reduce((total,time) =>{return total+time});
      clearInterval(this.timer);
      this.timer = null;
      this.timer = setInterval(() => {
        if (timeRemaining === 0) {
          clearInterval(self.timer);
          self.timer = null;
          self.totalTime = 0;
          self.timeLeft = 0;
          self.displayTime = "0:00";
          console.log("DONE!");
        } else {
          minutes = parseInt(timeRemaining / 60, 10);
          seconds = parseInt(timeRemaining % 60, 10);

          //add 0 to make 09,08,etc
          seconds = seconds < 10 ? "0" + seconds : seconds;

          timeRemaining--;
          if(timeRemaining%3 === 0 && self.percentageTick <4){
            self.percentageTick ++;
          }
          self.timeLeft = timeRemaining;
          self.displayTime = minutes + ":" + seconds;
          // console.log("time left", timeRemaining , 'from timer: ', self.timer )
        }
      }, 1000);
    },
    getRuntime() {
      this.timeArr = Runtime(this.steps);
    }
  },
  computed: {
    recipe() {
      return Store.getters.getCurrentRecipe;
    },
    steps() {
      return Store.getters.getBrewingRecipe.steps;
    },
    currentRecipeMeta() {
      return Store.getters.getBrewingRecipe;
    },
    manualCycle(){
      return (this.recipe.tags.indexOf("Maintenance") != -1 || this.recipe.tags.indexOf("maintenance") != -1) && 
        this.recipe.name.toLowerCase().indexOf('manual') != -1;
    },
    isServiceRec(){
      return (this.recipe.tags.indexOf("Service")!=-1 || (this.recipe.tags.indexOf("service")!=-1))
    },
    vwSize() {
      return window.screen.width > 500
        ? window.screen.width / 2
        : window.screen.width / 1.5;
    },
    percentage() {
      let length = !!this.steps ? this.steps.length: 0;
      return this.currentStep === 0
        ? 0
        : Math.round((this.currentStep + 1) / (length+1) * 100);
    },
    goBack() {
      return Store.getters.goBack;
    },
    sendWholeRecipe(){
      return !Store.getters.getSendIndividualSteps;
    },
    isSoundOn(){
      return Store.getters.isSoundOn;
    }
  },
  data() {
    return {
      timeLeft: 0,
      timeArr:[0],
      percentageTick: 0,
      displayTime: "---",
      totalTime: 0,
      currentStep: 0,
      abortOpened: false,
      dialogOpened: false,
      dialogText: "",
      dialogType:'',
      stepDialogText: "",
      isFinished: false,
      isError: false,
      errorCode: "",
      abortTimeout: null,
      completeTimeout: null,
      resetTimeout: null,
      timer: null
    };
  }
};
</script>


