const USER = { 
    state:{
        username:null,
        account_id:null,
        user_id:null,
        sync_number:null,
        ui_id: window.localStorage.getItem('ui_id')? window.localStorage.getItem('ui_id') : null,
        machine_id: window.localStorage.getItem('machine_id')? window.localStorage.getItem('machine_id') : null,
        mac:null,
        pin:'4576'
    },
    actions:{
        initUser(context){
            window.app.requestUser()
        },
        setUser(context, user){
            context.commit('setUser', user.username);
            context.commit('setAccount',user.creds.account_id);
            context.commit('setSyncNumber',user.creds.sync_number);
            context.commit('setUserId',user.creds.user_id);
            if(user.mac){
                context.commit('setMac', user.mac);
            }
            window.vue.$router.push('/')
            context.dispatch('getDeviceListFromWeb')
            window.app.isConnected()
            
        },
        logout(context){
            window.vue.$router.push('/login')
            context.commit('setUser',null);
            context.commit('setAccount',null);
            context.commit('setSyncNumber',null);
            context.commit('setUiId', null);
            context.commit('setMachineId', null);
            context.commit('setApprovedDevices', []);
            context.dispatch('clearRecipes')
            context.dispatch('clearView')
            context.dispatch('clearDevices')
            window.localStorage.removeItem('deviceList')
            window.localStorage.removeItem('ui_id');
            window.localStorage.removeItem('machine_id');
            window.localStorage.removeItem('recentRecs');
            window.localStorage.removeItem('currentCollection');
            window.localStorage.removeItem('currentFilters');
            window.app.disconnectBLE();
            window.app.clearBLE();
        }
    },
    mutations:{
        setUser(state, username){
            state.username = username;
        },
        setAccount(state, account_id){
            state.account_id = account_id;
        },
        setSyncNumber(state, sync_number){
            state.sync_number = sync_number;
        },
        setUiId(state,id){
            state.ui_id = id;
            window.localStorage.setItem('ui_id', id);
        },
        setMachineId(state,id){
            state.machine_id = id;
            window.localStorage.setItem('machine_id', id);
        },
        setUserId(state, id){
            state.user_id = id;
        },
        setMac(state, mac){
            state.mac = mac;
        },
        setPin(state, pin){
            state.Pin = pin;
        }
    },
    getters:{
        getUser(state){
            return state.username;
        },
        getAccount(state){
            return state.account_id;
        },
        getUserId(state){
            return state.user_id;
        },
        getSyncNumber(state){
            return state.sync_number;
        },
        getMac(state){
            return state.mac
        },
        getUserCreds(state){
            return {"account":state.account_id,"sync_number":state.sync_number}
        },
        getUiId(state){
            return state.ui_id;
        },
        getMachineId(state){
            return state.machine_id;
        },
        getPin(state, pin){
            return state.pin;
        }
    }
}

export default USER


