 const decodeStep = (step)=>{
    if(!!step){
        switch (true){
            case step.type.toLowerCase() == 'start':
                return 'Heating to ' + step.values.tmp +'˚C'
                break

            //Fill
            case step.type.toLowerCase() == 'fr':
            //perhaps convert the fill values to seconds and set a timeout to switch value to 'steeping' if a pause valuse exists.
                const pause = step.values.ap ? parseInt(step.values.ap) : 0;
                const fill = step.values.fwv ? Math.round(parseInt(step.values.fwv)/17) : 0;
                const rinse = step.values.rwv ? Math.round(parseInt(step.values.rwv)/17) : 0;
                if(pause > (fill+rinse)){
                    return 'Steeping '+step.values.ap+' sec';
                }else if(step.values.fwv && step.values.fwv != '' && parseInt(step.values.fwv) != 0){
                    return 'Filling ' + step.values.fwv +'ml';
                }else if(step.values.rwv && step.values.rwv != '' && parseInt(step.values.rwv) != 0){
                    return 'Rinsing '+ step.values.rwv +'ml';
                }else{
                    return 'Steeping '+step.values.ap+' sec'
                }
                break

            //Vacuum
            case step.type.toLowerCase() == 'vc':
                if(step.values.ps && step.values.ps != '' && parseInt(step.values.ps) != 0){
                    return 'Vacuum '+ step.values.ps + 'kPa';
                }else if(step.values.ap && step.values.ap != '' && parseInt(step.values.ap) != 0){
                    return 'Steeping '+step.values.ap+' sec';
                }else if(step.values.tm && step.values.tm != '' && parseInt(step.values.tm) != 0){
                    return 'Vacuum '+step.values.tm+' sec'
                }else{
                    return 'Steeping'
                }
                break

            //Purge
            case step.type.toLowerCase() == 'pg':
                    return 'Dispensing'
                break
            // Dialog
            case step.type.toLowerCase() == 'dialog':
                return 'Dialog'
                break

            //Command
            case !!step.values.cmd:
                return 'Sending Command'
                break

            // Blow Out
            case step.type.toLowerCase() == 'bo':
                return 'Finishing Recipe...'
                break

            default:
                console.log('Couldn\'t match step type:', step)
                break
        }
    }
}

export default decodeStep


