import appUpdate from '../../../scripts/eventBus'
import Logger from '../../../scripts/logger'
const VIEWS = {
    state:{
        collections:[],
        favorites: [],
        soundOn: window.localStorage.getItem('soundOn') == 'false' ?
                 false : 
                 true,
        recents: window.localStorage.getItem('recentRecs') ?
                 JSON.parse(window.localStorage.getItem('recentRecs')) : 
                 [],
        currentLayout: window.localStorage.getItem('layout') ? 
                    parseInt(window.localStorage.getItem('layout')) : 
                    0,
        currentCollection: !!window.localStorage.getItem('currentCollection') ? 
                            window.localStorage.getItem('currentCollection') : '',
        currentFilters: window.localStorage.getItem('currentFilters') ? 
                        JSON.parse( window.localStorage.getItem('currentFilters')):[],
        backRoute:'/',
        // descaleThresholds: {recipes:1500,operationHours:420,days:30},
        descaleThresholds: {recipes:1500,operationHours:420,days:30},
        descaleCounter: {recipes:0,operationHours:0,days:0},
        descaleRequired: false,
        isVideoOpened: false
    },
    actions:{
        setLayout(context, layout){
            context.commit('setLayout', layout);
            window.localStorage.setItem('layout', layout) 
        },
        clearView(context){
            context.commit('setFavorites', "[]");
            context.commit('setCollections', []);
        },
        checkDescale(context, metrics){
            console.log('checking descale metrics')
            context.commit('setDescaleCounter', metrics)
            let thresholds = context.getters.getDescaleThresholds;
            //check if descale counters triggers 'descale required'
            if( 
                parseInt(metrics.recipes) >= parseInt(thresholds.recipes) || 
                parseInt(metrics.days) >= parseInt(thresholds.days) ||
                parseInt(metrics.operationHours) >= parseInt(thresholds.operationHours)
            ){
                context.commit('setDescaleRequired', true)
                let trigger;
                //if wrapper isnt already aware of triggering, log descale triggered event
                if(parseInt(metrics.required) == 0 || metrics.required == false){
                    if(parseInt(metrics.recipes) >= parseInt(thresholds.recipes)) {
                        trigger = 300; 
                        console.log("**'Descale Required' triggered by drink count")
                    }else if(parseInt(metrics.operationHours) >= parseInt(thresholds.operationHours)){
                        trigger = 301;
                        console.log("**'Descale Required' triggered by op hours")
                    }else if(parseInt(metrics.days) >= parseInt(thresholds.days)){
                        trigger = 302;
                        console.log("**'Descale Required' triggered by days since last descale")
                    }
                    if(context.getters.isBLEConnected){
                        Logger.Log({type:5, event: trigger})
                        window.app.updateDescaleCounter(JSON.stringify({"key":"descaleRequired","value":true}))
                    }
                }
            }else{context.commit('setDescaleRequired', false)}
        },
        clearFilters(context){
            context.commit('setCurrentCollection','');
            context.commit('setCurrentFilters', null);
        }
    },
    mutations:{
        updateRoute(state, route){
            state.backRoute = route
        },
        setLayout(state, layout){
            state.currentLayout = layout;
        },
        toggleSounds(state){
            state.soundOn = !state.soundOn;
            window.localStorage.setItem('soundOn', state.soundOn);
        },
        updateRecents(state,id){
            state.recents.unshift(id);
            if(state.recents.length > 10){
                state.recents.splice(11, 1)
            }
            window.localStorage.setItem('recentRecs', JSON.stringify(state.recents))
        },
        setFavorites(state, favArr){
            if(favArr != JSON.stringify(state.favorites)){
                state.favorites = JSON.parse(favArr);
            }
        },
        setCollections(state, colArr){
            if(JSON.stringify(colArr) != JSON.stringify(state.collections)){
                state.collections = colArr;
            }
        },
        updateFavorites(state, id){
            if(state.favorites.indexOf(id) != -1){
                state.favorites.splice(state.favorites.indexOf(id), 1);
                console.log('removed ', id, "\n", state.favorites)
            }else{
                state.favorites.push(id);
                appUpdate.$emit('addedFav')
                console.log('added ', id, "\n", state.favorites)
            }
            window.app.updateFavorites(JSON.stringify(state.favorites))
        },
        setDescaleThresholds(state, obj){
            state.descaleThresholds = obj;
        },
        setDescaleCounter(state, obj){
            state.descaleCounter = obj;
        },setDescaleRequired(state, bool){
            state.descaleRequired = bool;
        },
        setCurrentCollection(state, collection){
            state.currentCollection = collection;
            window.localStorage.setItem('currentCollection', collection);
        },
        setCurrentFilters(state, filter){
            if(filter === null){
                state.currentFilters = [];
            }else if(state.currentFilters.indexOf(filter) != -1){
                console.log('already active')
                state.currentFilters.splice(state.currentFilters.indexOf(filter), 1);
            }else{
                console.log('new')
                state.currentFilters.push(filter);
            }
            window.localStorage.setItem('currentFilters', JSON.stringify(state.currentFilters));
        },
        setIsVideoOpened(state, bool){
            state.isVideoOpened = bool;
        }
    },
    getters:{
        isVideoOpened(state){
            return state.isVideoOpened
        },
        getCollections(state){
            return state.collections;
        },
        getCurrentCollection(state){
            return state.currentCollection;
        },
        getCurrentFilters(state){
            return state.currentFilters;
        },
        getCurrentLayout(state){
            return state.currentLayout;
        },
        getFavorites(state){
            return state.favorites;
        },
        getRecents(state, getters, rootState){
            // return state.recents;
            let recs = [];
            for(var i = 0; i<state.recents.length; i++){
                rootState.RECIPES.recipes.forEach(rec=>{
                    if(rec.id === state.recents[i]){
                        recs.push(rec);
                    }
                })
            }
            return recs
        },
        isSoundOn(state){
            return state.soundOn;
        },
        getDescaleThresholds(state){
            return state.descaleThresholds;
        },
        getDescaleCounter(state){
            return state.descaleCounter;
        },
        getDescaleRequired(state){
            return state.descaleRequired;
        },
        goBack(state){
            return state.backRoute;
        }
    }
}

export default VIEWS


