<template>
    <div class="sb-swiper gutter-tb" ref="sbSwiper">
        <div class="sb-swiper-slide flex gutter-tb"
            @touchstart="(e)=>dragX = e.touches[0].clientX"
            @touchmove="drag($event)" 
            @touchend="endDrag"
            :style="'transform: translate3d('+position+'px,0,0);opacity: '+opacity+';'"
            ref="sbSwiperSlide"
        >
            {{talkingPoints[slide]}}
        </div>
        <div class="sb-swiper-pagination flex">
            <span 
            :class="{'sb-swiper-dot':true, 'active': i === slide}"
            v-for="(point, i) in talkingPoints" :key="i"
            ></span>    
        </div>
    </div>
</template>

<script>
export default {
    props:{talkingPoints:Array},
    computed:{
        width(){
            return this.$refs.sbSwiper.offsetWidth;
        },
        opacity(){
            let calc = this.position === 0 ? '1' : (this.width/this.position)*.1 +'';
            return calc > 0 ? calc - .10 : calc* -1 - .10;
        }
    },
    methods:{
        drag(e){
            let distance = e.touches[0].clientX - this.dragX;
            if(distance > 0 && this.slide != 0){
                //moving to right
               this.position = distance < this.width? distance : this.width;
            }else if(distance > 0 && this.slide === 0){
                //moving to right on first slide
                this.position = distance < (this.width/5) ? distance : (this.width/5) ;
            }else if(distance < 0 && this.slide != this.talkingPoints.length-1 ){
                //moving to left
                this.position = distance > -this.width ? distance : -this.width;
            }else if(distance < 0 && this.slide === this.talkingPoints.length-1 ){
                //moving to left on last slide
                this.position = distance > -(this.width/5) ? distance : -(this.width/5);
            }
        },
        endDrag(){
            if(this.position > (this.width / 4) && this.slide != 0){
                console.log('finish dragright')
                this.loadNewSlide('back')
            }else if(this.position < -(this.width / 4) && this.slide != this.talkingPoints.length-1){
                console.log('finish drag left', -this.width);
                this.loadNewSlide('next')
            }
            this.dragX = 0;
            this.position = 0;
        },
        loadNewSlide(slide){
            if(slide === 'next'){
                this.slide += 1;
            }else if(slide === 'back'){
                this.slide -= 1;
            }
        }
    },
    data(){
        return {
            slide:0,
            dragX: 0,
            position:0
        }
    }
}
</script>

<style lang="scss" scoped>

</style>



