import Vue from 'vue'
import Router from 'vue-router'
// import Login from '@/pages/Login'
import Home from '@/pages/Home'
// import Confirm from '@/pages/Confirm'
import Run from '@/pages/Run'
// import IndividualSteps from '@/pages/IndividualSteps'
// import System from '@/pages/System'
// import Connect from '@/pages/Connect'
// import Maintenance from '@/pages/Maintenance'
import NotFound from '@/pages/404'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    // {
    //   path: '/login',
    //   name: 'Login',
    //   component: Login
    // },
    {
      path: '/run/:name/:size',
      name: 'Run',
      component: Run
    },
    // {
    //   path: '/individualsteps/:name/:size',
    //   name: 'IndividualSteps',
    //   component: IndividualSteps
    // },
    // {
    //   path:'/system',
    //   name:'System',
    //   component:System
    // },
    // {
    //   path: '/maintenance',
    //   name: 'Maintenance',
    //   component: Maintenance
    // },
    // {
    //   path: '/connect',
    //   name: 'Connect',
    //   component: Connect
    // },
    {
      path:'*',
      name: '404',
      component: NotFound
    }
  ]
})



