<template>
  <div :class="{'flex':true,'notification':true, 'notification__active': isOpened, 'iphonex-notify': isiPhoneX && isOpened}" @touchstart="closeNotification($event)">
    <div class="flex notification__inset" style="justify-content: flex-start;">
      <div class="notification-dots-wrapper flex">
        <!-- <img class="notification-dots" src="../../assets/bkon-dots.png" alt=""> -->
      </div>
      <div class="notification__message-wrapper">
        <span>{{message}}</span>
      </div>
      <div class="notification__close-circle">
        <i class="bcon-close notification__close"></i>
      </div>
    </div>
  </div>
</template>

<script>
import appUpdate from '../../scripts/eventBus.js'
import Store from '../../store'
export default {
    created(){
        appUpdate.$on('notify',($event)=>{
            this.notifyUser($event)
             if(this.isSoundOn){
                window.app.playSound('notification');
            }
        })
    },
    beforeDestroy(){
        appUpdate.$off("notify");
    },
    methods:{
        notifyUser(message){
            this.message = message;
            this.isOpened = true;
            let self = this;
            this.notifyTimeout = setTimeout(function(){
                self.isOpened = false;
                let that = self;
                setTimeout(function(){
                that.message = '';
                },200)
            },4000)
        },
        closeNotification(e){
            e.stopPropagation();
            this.message = '';
            this.isOpened = false;
            clearTimeout(this.notifyTimeout);
        }
    },
    computed:{
        isSoundOn(){
            return Store.getters.isSoundOn;
        },
        isiPhoneX(){
           return Store.getters.isiPhoneX;
        }
    },
    data(){
        return{
                message: '',
                isOpened: false,
                notifyTimeout: null
        }
    }
}
</script>
<style lang="scss">
$bk-white: #fcfcfc;
$bk-orange: #ed3721;
$bk-gray: #3d3e39;
.notification{
    position: absolute;
    top: -15vh;
    transition: .3s;
    width: 100vw;
    min-height: 10vh;
    margin: auto;
    display: block;
    z-index: 6000;
    // -webkit-box-shadow: 0px 0px 35px 0px rgba(33,33,30,1);
    // -moz-box-shadow: 0px 0px 35px 0px rgba(33,33,30,1);
    // box-shadow: 0px 0px 35px 0px rgba(33,33,30,1);

    &__active{
        transition: .3s;
        transform: translate3d(0,17vh,0);
    }

    &__inset{
        position: relative;
        width: 95%;
        height: 100%;
        border-radius: 10px;
        margin: auto;
        background: $bk-gray;
        -webkit-box-shadow: 0px 0px 35px 0px rgba(33,33,30,1);
        -moz-box-shadow: 0px 0px 35px 0px rgba(33,33,30,1);
        box-shadow: 0px 0px 35px 0px rgba(33,33,30,1);

    }

    &-dots{
        height: 7vw;
        padding-right: 10px;
        border-right: 1px solid rgba($bk-white, .3);
        &-wrapper{
        width: 15%;
        margin: 10px;
        }
    }

    &__close{
        font-size: 12px;
        color: $bk-orange;
        padding: 5px;
        &-circle{
            position: absolute;
            top: 2px;
            right: 2px;
            margin: 5px;
            border-radius: 50%;
            border: 1px solid $bk-orange;
        }
    }

    &__message{
        &-wrapper{
        //width: 85%;
        margin-right:26px;
        box-sizing: border-box;
        padding: 5px;
        font-size: 4vw;
        }
    }
}

</style>



