<template>
    <section :class="{'rec-modal-wrapper flex':true, 'rec-modal-wrapper__video':isVideoOpened,'rec-modal-wrapper__story':isStoryOpened}">
        <div :class="{'rec-modal':true}">
            <div ref="recModalContent" class="rec-modal-content-wrapper" v-if="!isVideoOpened && !isStoryOpened">
                <i v-if="onMainView" class="bcon-times rec-modal-close" @click="()=>$emit('close')"></i>
                <!-- image -->
                <section class="rec-modal-image" :style="!!recipe.images ?`background-image: url(${require('@/assets/recipes/'+recipe.images[1])})`:''">
                    <div class="recipes-card-info">
                            <h3>{{recipe.name}}</h3>   
                            <div class="flex-start" style="width: 100%"><i class="bcon-tag"></i><p class="flex rec-modal-tags">{{recipe.tags.join(', ')}}</p></div> 
                    </div>
                </section>
                <!-- image end 
                **************
                content start-->
                <section v-if="!view" class="rec-modal-content" :class="{'rec-modal-content--full': isIngredientOpened}">
                    <!-- Main info view -->
                    <div v-if="onMainView" id="selectedRecipeInfo" style="width:100%;height:100%;position:relative;">
                        <!-- Summary section -->
                        <Summary 
                            :recipe="recipe"
                            :showDetails="showDetails" :isVideo="true" :isStory="true" :isIngredient="true" 
                            @toggleDetails="toggleDetails" 
                            @launchStory="()=>isStoryOpened = true"
                            @launchVideo="setIsVideoOpened(true)" 
                            @launchIngredient="()=>isIngredientOpened = true"
                        />
                        <div class="rec-modal-divider" style="width: calc(100% - 72px);"></div>
                        <!-- Portions -->
                        <portions :portions="recipe.recipes" @select="($event)=>{portionIndex = $event; isConfirmOpened = true;}"/>
                    </div>

                    <Nutrition v-if="isIngredientOpened" :recipe="recipe" @close="isIngredientOpened = false"/>
                </section>
                <!-- 
                    SECTION 2
                 -->
                 <section v-if="view" class="rec-modal-halves rec-modal-content flex" :class="{'rec-modal-content--full': isIngredientOpened}">
                     <div v-if="onMainView" class="half gutter">
                         <p :class="{'rec-summary sumhalf':true}">
                            Premium, lush green tea blended with hints of spearmint, lemon verbena and lemongrass. It’s a just-right balance of cool calm and bright invigoration.
                        </p>
                        <div class="flex-spread rec-modal-buttons gutter-top">
                            <!-- <i :class="{'bcon-chevron-right rec-modal-chev':true,}"></i> -->
                            <div class="flex-start">
                                <!-- info icon -->
                                <button class="rec-modal-info-button flex" @click="(e)=>{e.stopPropagation(); isStoryOpened = true;}">
                                    <i class="bcon-bars"></i>
                                </button> 
                                <!-- video icon -->
                                <button class="rec-modal-info-button flex" @click="(e)=>{e.stopPropagation(); setIsVideoOpened(true);}">
                                    <i class="fas fa-video"></i>
                                </button> 
                                <!-- ingredient icon -->
                                <button class="rec-modal-info-button flex" @click="(e)=>{e.stopPropagation(); isIngredientOpened = true;}">
                                    <i class="fas fa-apple-alt"></i>
                                </button> 
                            </div>
                        </div>
                     </div>
                    <div class="half gutter-bottom" v-if="onMainView">
                        <portions :view="view" :portions="recipe.recipes" @select="($event)=>{portionIndex = $event; isConfirmOpened = true;}"/>
                    </div>
                    <Nutrition v-if="isIngredientOpened" :recipe="recipe" @close="isIngredientOpened = false"/>
                 </section>
            </div>

            <video-player v-if="isVideoOpened" :videos="recipe.videos" :currentVideo="currentVideo" @resetVideo="currentVideo=0"/>
            <section v-if="isVideoOpened && recipe.videos.length > 1" class="flex" style="overflow:scroll">
                <div v-for="(videoURL,i) in recipe.videos" :key="i" :class="{'rec-video-player__thumbnail flex':true, 'active':i === currentVideo}">
                    <img :src="getThumbnail(videoURL)" @click="currentVideo = i">
               </div>
            </section>

            <recipe-story v-if="isStoryOpened" :recipe="recipe" @close="isStoryOpened = false"/>
            <Confirm v-if="isConfirmOpened" 
                :recipe="recipe" 
                :portionIndex="portionIndex"
                :parentRecipeName="recipe.name"
                @back="()=>{portionIndex = null; isConfirmOpened = false;}"
            />
        </div>
    </section>
</template>

<script>
import Store from "../store"
import Summary from './recipeModalViews/Summary.vue'
import Portions from './recipeModalViews/Portions.vue'
import VideoPlayer from './recipeModalViews/videoPlayer.vue'
import RecipeStory from './recipeModalViews/RecipeStory.vue'
import Nutrition from './recipeModalViews/Nutrition.vue'
import Confirm from './recipeModalViews/Confirm.vue'
export default {
    props:{view:Boolean},
    components:{Summary, Portions, VideoPlayer, RecipeStory, Nutrition, Confirm},
    computed:{
        recipe(){
            return Store.getters.getCurrentRecipe;
        },
        isVideoOpened(){
            return Store.getters.isVideoOpened;
        },
        onMainView(){
            return !this.isVideoOpened && !this.isStoryOpened && !this.isIngredientOpened;
        }
    },
    methods:{
        toggleDetails(){
            this.showDetails = !this.showDetails;
            window.localStorage.setItem('showDetails', this.showDetails)
        },
        setIsVideoOpened(isVideoOpened){
            Store.commit('setIsVideoOpened', isVideoOpened);
        },
        getThumbnail(url){
            let embedURL = url.replace('watch?v=','embed/');
            let videoId = embedURL.split('/')[embedURL.split('/').length -1];
            return `http://img.youtube.com/vi/${videoId}/0.jpg`
        }
    },
    data(){
        return{
            showDetails: window.localStorage.getItem('showDetails') ? window.localStorage.getItem('showDetails') ==='true' : false,
            isStoryOpened: false,
            isIngredientOpened: false,
            isConfirmOpened: false,
            portionIndex: null,
            currentVideo:0
        }
    }
}
</script>



