const validator = (list, device)=>{
    console.log('validating connection...')
    let i = 0;
    let isApproved = false;
    while(i < list.length && !isApproved){
        if(list[i].mac === device.mac){
            isApproved = true;
            break;
        }
        i+=1;
    }

    return isApproved;
}

export default validator;


