<template>
<div ontouchstart="">
    <div class="recipes-tea-wrapper__outer" v-for="(rec,index) in recipes" 
    :key="rec.id +'-l-'+index" :class="defineType(rec.type) + '-bg'" 
    >
        <!-- <router-link :to="'/confirm/'+encodeURIComponent(rec.name)">  -->
            <div class="recipes-tea-wrapper__inner flex-spread" @click="updateRoute(rec.name, rec.id)"> 
                <div style="max-width: 85%; overflow:hidden" class="flex-start">
                    <div class="recipes-list-image-wrapper">
                        <img class="recipes-list-image" :src="!!rec.images ?`${require('@/assets/recipes/'+rec.images[1])}`:''" alt="">
                    </div>
                    <div style="display:inline-block;">
                        <div class="flex recipes-name-wrapper">
                            <span v-if="showIndex" style="margin-right: 1vw;">{{index +1}}</span>
                            <h2 class="recipes-title">{{rec.name}}</h2>
                        </div>
                        <div class="flex recipes-tags-wrapper" style="justify-content: flex-start;">
                            <i class="bcon-tag recipes-tags"></i>
                            <span class="recipes-tags">{{stringifyTags(rec.tags)}}</span>
                        </div>
                    </div>
                </div>

                <div class="flex-end">
                    <!-- info icon -->
                    <div v-if="!!rec.text[1]"  class="recipes-indicator flex">
                        <i class="bcon-bars"></i>
                    </div> 
                    <!-- video icon -->
                    <div v-if="!!rec.videos.length" class="recipes-indicator flex">
                        <i class="fas fa-video"></i>
                    </div> 
                    <!-- ingredient icon -->
                    <div v-if="!!parseInt(rec.ingredients)" class="recipes-indicator flex">
                        <i class="fas fa-apple-alt"></i>
                    </div> 
                    <!-- <div class="flex" style="height:10vw;" @click="favorite($event,rec.id)">
                        <i v-if="favorites.indexOf(rec.id) != -1" class="bcon-star recipes-favorite-selected"></i>
                        <i v-else class="bcon-star-o recipes-favorite"></i>
                    </div> -->
                    <!-- <div class="recipes-type recipes-type-circle flex" :class="defineType(rec.type) + '-circle'"> -->
                        <!-- <i v-if="rec.type == '1'" class="bcon-leaf recipes-type-icon"></i>
                        <i v-else-if="rec.type == '2'" class="bcon-coffee-bean recipes-type-icon"></i>
                        <i v-else-if="rec.type == '3'" class="bcon-glass recipes-type-icon"></i>
                        <i v-else-if="rec.type == '4'" class="bcon-iced-drink recipes-type-icon"></i>
                        <i v-else class="bcon-bowl recipes-type-icon"></i> -->

                    <!-- </div> -->
                </div>
            </div>
        <!-- </router-link> -->
    </div>
</div>
</template>

<script>
import Store from '../../store'
export default {
    props:{recipes:Array, showIndex:Boolean},
    methods:{
        favorite(e,id){
            e.stopPropagation()
            Store.commit('updateFavorites',id)
        },
        defineType(type){
            switch(true){
                case(type == "1"):
                    return 'tea'
                    break;
                case(type == "2"):
                    return 'coffee'
                    break;
                case(type == "3"):
                    return 'spirit'
                    break;
                case(type == "4"):
                //no infusions. 4 is wine
                    return 'infusion'
                    break;
                default:
                    return 'other'
            }   
        },
        stringifyTags(tags){
            let str = "";
            tags.forEach((x,index) => {
                if(index == tags.length-1){
                    str+= x;
                }else{
                    str+= (x + ', ');
                }
            })
            return str
        },
        updateRoute(name,id){
            this.$emit('handleSelection', id)
            // console.log('update route: ', '/confirm/'+encodeURIComponent(name))
            // Store.commit('updateRoute', '/confirm/'+encodeURIComponent(name))
            // this.$emit('selectRecipe', id);
            // this.$router.push('/confirm/'+encodeURIComponent(name))
        }
    },
    computed:{
        favorites(){
            return Store.getters.getFavorites;
        }
    }
}
</script>

<style>

</style>



