<template>
    <section class="rec-nutrition gutter">
        <i class="bcon-times rec-nutrition-close" @click="()=>$emit('close')"></i>
        <section >
            <h3 class="rec-nutrition-title">{{recipe.name}}</h3>   
            <div class="flex-start" style="width: 100%; opacity:.7;"><i class="bcon-tag"></i><p class="flex rec-story-tags">{{recipe.tags.join(', ')}}</p></div>
            <div class="rec-modal-divider" style="margin-top: 14px"></div>
        </section>
        <section class="flex-spread rec-nutrition-sections">
            <div :class="{'flex':true, 'rec-nutrition-sections--active': active == 3}" @click="setView(3)">Ingredients</div>
            <div :class="{'flex':true, 'rec-nutrition-sections--active': active == 1}" @click="setView(1)">Nutrition</div>
            <div :class="{'flex':true, 'rec-nutrition-sections--active': active == 2}" @click="setView(2)">Allergens</div>
        </section>
         <div class="rec-modal-divider" style="margin-top: 14px"></div>
         <section>
             <!-- nutrition list -->
             <!-- <ul class="rec-ingredient-list" v-show="active == 1">
                 <li v-for="(value, key) in nutrition" :key="key" class="flex-spread">
                     <div>
                         <p>{{decodeKey(key)}}</p>
                     </div>
                     <div v-if="key == 'calories'" class="rec-ingredient-list-value">
                         {{value}}
                     </div>
                     <div v-else class="rec-ingredient-list-value">
                         {{value}}{{key == 'caffeine' || key == 'sodium' || key == 'cholesterol' ? 'mg' : 'g'}}
                     </div>
                 </li>
             </ul> -->
             <div class="rec-nutrition-text gutter-tb" v-show="active == 1">
                 {{recipe.nutrition}}
             </div>
             <!-- alergens -->
             <ul class="rec-ingredient-list" v-show="active == 2">
                 <li v-if="calculateAllergens(ingredients).length >0" v-for="(item, i) in calculateAllergens(ingredients)" :key="i" >
                     <div>
                         <p>{{item.name}}</p>
                         <span><i>{{item.text}}</i></span>
                     </div>
                 </li>
                 <div v-if="calculateAllergens(ingredients).length ===0" class="rec-ingredient-list-empty gutter-tb">
                    <p>there are no alergens listed</p>
                 </div>
             </ul>
             <!--  Ingredients list -->
             <!-- <ul class="rec-ingredient-list" v-show="active == 3">
                 <li v-for="(item, i) in recipe.recipes[currentPortion].ingredients" :key="i" class="flex-spread">
                     <div>
                         <p>{{item.name}}</p>
                         <span><i>{{item.text}}</i></span>
                     </div>
                     <div class="rec-ingredient-list-value">
                         {{item.value}}
                     </div>
                 </li>
             </ul> -->
             <div class="rec-nutrition-text gutter-tb" v-show="active == 3">
                 {{recipe.ingredients}}
             </div>
         </section>
    </section>
</template>

<script>
export default {
    props:{recipe:Object},
    mounted(){
        this.calculateNutrition(this.recipe.recipes[this.currentPortion].ingredients);
    },
    watch:{
        currentPortion(newVal, oldVal){
             this.calculateNutrition(this.ingredients);
        }
    },
    computed:{
        ingredients(){
            return this.recipe.recipes[this.currentPortion].ingredients;
        }
    },
    methods:{
        setView(view){
            this.active = view;
            window.localStorage.setItem('nutritionView', view);
        },
        calculateAllergens(ingredients){
            let allergens = [];
            ingredients.forEach(x=>{
                if(!!parseInt(x.isAllergen)){
                    allergens.push(x);
                }
            })
            console.log('allergy', allergens)
            return allergens;
        },
        calculateNutrition(ingredients){
            this.resetNutrition();
            let $this = this;
            ingredients.forEach(ing=>{
            $this.nutrition.calories += !!ing.cal ?  parseInt(ing.cal) : 0;
            $this.nutrition.totalFat +=  !!ing.totFat ?  parseInt(ing.totFat) : 0;
            $this.nutrition.saturatedFat +=  !!ing.satFat ?  parseInt(ing.satFat) : 0;
            $this.nutrition.transFat += !!ing.transFat ?  parseInt(ing.transFat) : 0;
            $this.nutrition.cholesterol += !!ing.chol ?  parseInt(ing.chol) : 0;
            $this.nutrition.sodium += !!ing.sodium ?  parseInt(ing.sodium) : 0;
            $this.nutrition.totalCarb += !!ing.totCarb ?  parseInt(ing.totCarb) : 0;
            $this.nutrition.dietaryFiber += !!ing.dietFiber ?  parseInt(ing.dietFiber) : 0;
            $this.nutrition.sugars +=  !!ing.sugars ?  parseInt(ing.sugars) : 0;
            $this.nutrition.protien +=  !!ing.protien ?  parseInt(ing.protien) : 0;
            $this.nutrition.caffeine +=  !!ing.caffeine ?  parseInt(ing.caffeine) : 0;
            });
        },
        resetNutrition(){
            this.nutrition.calories = 0;
            this.nutrition.totalFat =  0;
            this.nutrition.saturatedFat =  0;
            this.nutrition.transFat = 0;
            this.nutrition.cholesterol = 0;
            this.nutrition.sodium = 0;
            this.nutrition.totalCarb = 0;
            this.nutrition.dietaryFiber = 0;
            this.nutrition.sugars =  0;
            this.nutrition.protien =  0;
            this.nutrition.caffeine =  0;
        },
        decodeKey(key){
            switch (true) {
                case key =='calories':
                    return "Calories"
                    break;
                case key == 'totalFat': 
                    return "Total Fat"
                    break;
                case key == 'saturatedFat':
                    return "Saturated Fat";
                    break; 
                case key == 'transFat':
                    return "Trans Fat"
                    break;
                case key == 'cholesterol':
                    return 'Cholesterol';
                    break;
                case key == 'sodium':
                    return "Sodium";
                    break;
                case key == 'totalCarb':
                    return "Total Carbohydrate"
                    break;
                case key == 'dietaryFiber':
                    return "Dietary Fiber";
                    break;
                case key == 'sugars':
                    return 'Sugars';
                    break;
                case key == 'protien':
                    return 'Protien'
                    break;
                case key == 'caffeine':
                    return 'Caffeine'
                    break;

                default:
                    break;
            }
        }
    },
    data(){
        return {
            currentPortion:0,
            active: window.localStorage.getItem('nutritionView') ? window.localStorage.getItem('nutritionView') : 1,
            nutrition:{
                calories:0,
                totalFat: 0,
                saturatedFat: 0,
                transFat:0,
                cholesterol:0,
                sodium:0,
                totalCarb:0,
                dietaryFiber:0,
                sugars: 0,
                protien: 0,
                caffeine: 0,
            }
        }
    }
}
</script>



