<template>
    <div class="filter-collections" 
        @touchstart="startDrag($event)"
        @touchmove="drag($event)" 
        @touchend="endDrag"
    >
        <!-- Top bar -->
        <div class="panel-bar flex">
            <p>Collections</p>
        </div>
        <!-- search bar -->
        <div class="filter-search-bg flex">
            <input v-if="isOpened" class="filter-search" type="text" v-model="searchTerm">
            <i v-show="this.searchTerm != ''" class="bcon-close" @click="clearSearch"></i>
        </div>
        <!-- Active Filters -->
        <p class="block-title">Active Collection</p>
        <div class="filter-selected-wrapper flex">
            <div v-if="selectedCollection.length === 0" class="filter-selected-empty">No collection selected</div>
            <div v-else-if="typeFilters.indexOf(selectedCollection) != -1" class="filter-chip flex"
                    :class="filterClass(selectedCollection)" @click="selectFilter(selectedCollection)"
            >
                <div class="flex">
                    <p style="padding-right:1vw;">{{selectedCollection}}{{selectedCollection == 'other' ? '' : 's'}}</p>
                    <i class="bcon-times-circle"></i>
                </div>
            </div>
            <div v-else class="filter-chip filter-chip__active filter-chip__active-user flex"
                 @click="selectFilter(selectedCollection)"
            >
                <div class="flex">
                    <p style="padding-right:1vw;">{{selectedCollection.replace("$","")}}</p>
                    <i class="bcon-times-circle"></i>
                </div>
            </div>
            <div v-if="selectedCollection.length != 0" class="filter-chip filter-chip-spacer"></div>
        </div>  

        <div class="filter-divider"></div>

        
        <!-- Filter list -->
        <div style="padding: 10px 1vw;" v-if="searchTerm === ''">
            <div class="filter-chip" v-for="filter in typeFilters" :key="filter"
                :class="filterClass(filter)" @click="selectFilter(filter)"
            >
                <p>{{filter}}{{filter == 'other' ? '' : 's'}}</p>
            </div>
            <div class="filter-chip filter-chip__user" v-for="filter in collections" :key="filter"
                 @click="selectFilter(filter)"
            >
                <p>{{filter.replace("$", "")}}</p>
            </div>
        </div>
        <div style="padding-top: 10px;" v-else>
            <div v-if="searchedFilters.length == 0" class="filter-selected-empty">
                no collections match search :(
            </div>
            <div v-else class="filter-chip" v-for="filter in searchedFilters" :key="filter"
                :class="filterClass(filter)" @click="selectFilter(filter)"
            >
                <p>{{filter}}</p>
            </div>
        </div>
    </div>
</template>

<script>
import appUpdate from '../../scripts/eventBus'
import Store from '../../store'
export default {
    props:{
        isOpened:Boolean,
        selectedCollection: String,
        dragDistance:Number
    },
    computed:{
        collections(){
            return Store.getters.getCollections;
        },
        searchedFilters(){
            let self = this;
            let search = this.searchTerm.trim().toLowerCase();
            let filterSearch = this.collections.filter(filt => filt.toLowerCase().indexOf(search) != -1);
            return this.typeFilters.filter(filt => filt.toLowerCase().indexOf(search) != -1).concat(filterSearch)
        },
    },
    methods:{
        filterClass(type){
            if(this.selectedCollection == type){
                return 'filter-chip__active filter-chip__active-' + type;
            }else{
                return 'filter-chip__' + type;
            }
        },
        selectFilter(filter){
            this.$emit('selectFilter',filter);
            this.searchTerm = '';
        },
        clearSearch(){
            this.searchTerm = '';
        },
        startDrag(e){
            this.dragX = e.touches[0].clientX;
        },
        drag(e){
            let distance = e.touches[0].clientX - this.dragX
            if(distance < 0){
                this.closeOnEndTouch = false;
                const pos = distance > -350? distance : -350;
                this.$emit('updatePosition', pos);
            }else if(distance > 90){
                this.closeOnEndTouch = true;
            }
        },
        endDrag(){
            if(this.dragDistance < -50){
                this.$emit('updatePosition', -350);
                this.$emit('switchView', -350);
            }else{
                this.$emit('updatePosition', 0);
            }
            if(this.closeOnEndTouch){
                appUpdate.$emit('panelRight', false)
                this.closeOnEndTouch = false;
            }
            this.dragX = 0;
        },
    },
    data(){
        return{
            searchTerm: '',
            dragX:0,
            closeOnEndTouch: false,
            typeFilters: ['tea','coffee','spirit','infusion','other']
        }
    }
}
</script>

<style>

</style>



