import recipes from "../../../sysRecipes";
import collectionsExtractor from  "../../../scripts/collectionTagExtractor"
import Moment from "../../../scripts/dateFormatter";
import Logger from  "../../../scripts/logger"
import convertRecipe from "../../../scripts/recipeConversion"

const RECIPES = {
    state:{
        recipes: [],
        currentRecipe:{sequences:[{name:''}]},
        stagedLog: null,
        recToMachine: null,
        systemRecs: [],
        sendIndividualSteps: false,
        isBrewing: false,
    },
    actions:{
        fetchRecipe(context, obj){
            console.log('fetching recipe: ', obj.id);
            let payload = {"account": obj.account,"machine": context.getters.getConnectedDevice.mac,"id": obj.id}
            window.app.fetchRecipe(JSON.stringify(payload));
            // window.app.fetchRecipe(JSON.stringify({"account": "0", "id": obj.id}));
        },
        setCurrentRecipe(context, rec){
            console.log("STORE: attempting to set current rec")
            // context.commit('setCurrentRecipe', {})
            context.commit('setCurrentRecipe', rec)
        },
        clearRecipes(context){
            context.commit('setRecipes', []);
            context.commit('setSystemRecs',[]);
            context.commit('setCurrentRecipe', {sequences:[{name:''}]});
            context.commit('setRecToMachine', null);

        },
        startBrewing(context, obj){
            context.commit('setSendIndividualSteps', false);
            context.commit('setIsBrewing', true)
            let finalRec = {steps:[]}
            let isBlowOut = false;
            console.log("Start Brewing called: ", obj)
            // check for blow out step and purge dialogs 
            obj.steps.forEach(function(step){
                // remove "0" values
                for(var key in step.values){
                    if(parseInt(step.values[key]) === 0 && 
                    (key.toLowerCase() === "fwv" ||
                    key.toLowerCase() === "rwv" ||
                    key.toLowerCase() === "ap" ||
                    key.toLowerCase() === "ps" ||
                    key.toLowerCase() === "tm" ||
                    key.toLowerCase() === "dl")
                    ){
                        delete step.values[key]
                    }else{
                        //make sure all values are strings;
                        step.values[key] = step.values[key] + "";
                    }
                }
                if(step.type.toLowerCase() == 'bo') {
                    isBlowOut = true;
                }
                if (step.type.toLowerCase() == 'pg' && parseInt(step.values.manstop) == 1){
                    var nobj = {type:'pg',values:{}};
                    nobj.values.dialog = "Manually stop the purge or it will close when it\'s finished";
                    if(step.values.det){nobj.values.det = step.values.det + ""};
                    if(step.values.ps) {nobj.values.ps = step.values.ps + "";}
                    if(step.values.tm){nobj.values.tm = step.values.tm + "";}
                    finalRec.steps.push(nobj);
                }else if (step.type == 'pg' && parseInt(step.values.manstop) == 0){
                    let rmManstop = step
                    delete rmManstop.values['manstop'];
                    finalRec.steps.push(rmManstop)
                }else if(step.type.toLowerCase() == 'dialog'){
                    // console.log("in dialog checker \n", step)
                    let dialog = JSON.parse(JSON.stringify(step));
                    // console.log("new Dialog: ", dialog)
                    let textKey = dialog.values.text ? 'text' : 'TEXT'
                    dialog.values[textKey] = encodeURIComponent(dialog.values[textKey]).replace(/'/g, "%27");
                    finalRec.steps.push(dialog);
                }else if(!!step.values.cmd){
                    context.commit('setSendIndividualSteps', true);
                    finalRec.steps.push(step)
                    // console.log("recipe contained CMD step.  Sending steps individually")
                }else{
                    finalRec.steps.push(step);
                }
            })
        
            if(!isBlowOut && parseInt(obj.account) != 0) {
              finalRec.steps.push({type:'bo', values:{bt:"4"}})
            }
            let recType;
            //if maintenance recipe
            if(obj.name && obj.account == 0){
                if(obj.name.toLowerCase() == 'clean in place'){
                    recType = 3;
                    window.app.updateCount('cip')
                }else if(obj.name.toLowerCase() == 'descale'){
                    window.app.updateCount('descale')
                    recType = 3;
                }else if(obj.name.toLowerCase() == 'manual rinse'){
                    window.app.updateCount('manRinse')
                    recType = 3;
                }else if(obj.name.toLowerCase() == 'manual purge'){
                    window.app.updateCount('manPurge')
                    recType = 3;
                }else{
                    //is service test
                    recType = 4;
                }
            }else{
                window.app.updateCount('recipes')
                context.commit('updateRecents', obj.id)
                recType = 2;
            }
            finalRec.id = obj.variation_id ? obj.variation_id : obj.id;
            finalRec.name = obj.name;
            finalRec.size = obj.variation_name; 
            finalRec.type = recType;
            finalRec.steps = finalRec.steps;
            context.commit('setRecToMachine', finalRec)
            //Log Recipe start. 
            //If drink recipe, log variation_id, else log top-level/meta id (for system recs)
            Logger.Log(
                {type: recType, recipe_id: obj.variation_id ? obj.variation_id : obj.id, event:100,
                meta:{"name":encodeURIComponent(obj.name),"size":encodeURIComponent(obj.variation_name), "action":"Start"}}
            )
            if(context.getters.getSendIndividualSteps){
                console.log('pushing indv router: ', obj.route)
                window.vue.$router.push({ name: 'IndividualSteps', params: { name: encodeURIComponent(obj.name), size:'Maintenance' }});
            }else{
                console.log('pushing router: ', obj.route)
                window.vue.$router.push(obj.route);
            }
        },
        killRecipe(context){
            console.log('killing brew state');
            context.commit('setSendIndividualSteps', false);
            context.commit('setIsBrewing', false)
            context.commit('setRecToMachine',null);
        },
        setRecipes(context, recs){
            context.commit('setRecipes', recs);
            if(context.getters.getIsUpdate && context.getters.getIsSyncing == 'updating'){
                context.commit('setIsSyncing', null)
                context.commit('setIsUpdate', false)
            }
            let cTags = collectionsExtractor(recs);
            context.commit('setCollections', cTags);
        }
    },
    mutations:{
        setRecipes(state, recs){
            console.log("Setting recipes in Store:");
            state.recipes = recs;

        },
        setSystemRecs(state, recs){
            state.systemRecs = recs;
        },
        setCurrentRecipe(state, rec){
            // Object.assign(state.currentRecipe, rec);
            state.currentRecipe = rec;
            console.log("STORE: Set current rec to: \n", rec)
        },
        setStagedLog(state, entry){
            // Need to attach the connected machine's identifier to log object
            state.stagedLog = entry;
        },
        setRecToMachine(state, index){
            state.recToMachine = index;
        },
        setIsBrewing(state, bool){
            console.log("UI brewing state is now ", bool);
            state.isBrewing = bool
        },
        setSendIndividualSteps(state,bool){
            state.sendIndividualSteps = bool;
        }
    },
    getters:{
        tags(state){
            let tagList = [];
            state.recipes.forEach(rec => {
                //check each recipe's tags and add to tag list if not already there
                for(var i=0; i<rec.tags.length; i++){
                    if(tagList.indexOf(rec.tags[i]) == -1){
                        tagList.push(rec.tags[i]);
                    } 
                }
            });
            //sort alphabetically
            // let sorted = tagList.sort((a, b)=>{
            //     if(a < b) return -1;
            //     if(a > b) return 1;
            //     return 0;
            // })
            return tagList;
        },
        getIsBrewing(state, bool){
            return state.isBrewing;
        },
        getRecipes(state){
            return state.recipes;
        },
        getCurrentRecipe(state){
            return state.currentRecipe;
        },
        getBrewingRecipe(state){
            return state.recToMachine;
        },
        getStagedLog(state){
            return state.stagedLog;
        },
        getRecipeCount(state){
            return state.recipes.length;
        },
        getMaintenanceRecs(state){
            return state.systemRecs.filter(rec=>{
                return rec.tags.indexOf('maintenance') != -1;
            });
        },
        getServiceRecs(state){
            return state.systemRecs.filter(rec=>{
                return rec.tags.indexOf('service') != -1;
            });
        },
        getSendIndividualSteps(state){
            return state.sendIndividualSteps;
        },
        getSystemRecs(state){
            return state.systemRecs.filter(rec=>{
                return rec.tags.indexOf('service') != -1;
            });
        }
    }
}

export default RECIPES


