<template>
    <div style="height:100%;">
        <section v-for="(tag,i) in tags" :key="i">
            <div class="recipes-section gutter">
                <h2 class="">{{tag}}</h2>
                <div class="rec-divider"></div>
            </div>
            <list-view :recipes="recipes.filter(rec=> rec.tags.indexOf(tag) != -1)" @handleSelection="(id)=>$emit('handleSelection', id)" />
        </section>
    </div>
</template>

<script>
import ListView from './ListView.vue'
export default {
    props:{recipes:Array, tags:Array, viewVersion:Number},
    components: {ListView},
    computed:{
        teas(){
            return this.recipes.filter(rec=>{
                return rec.type == '1'
            })
        },
         coffees(){
            return this.recipes.filter(rec=>{
                return rec.type == '2'
            })
        },
         spirits(){
            return this.recipes.filter(rec=>{
                return rec.type == '3'
            })
        },
         infusions(){
            return this.recipes.filter(rec=>{
                return rec.type == '4'
            })
        },
         other(){
            return this.recipes.filter(rec=>{
                return rec.type == '5'
            })
        },
    },
    methods:{
        selectRecipe(id){
            this.$emit('selectRecipe', id)
        },
    },
}
</script>

<style>

</style>



