<template>
    <div class="nav flex-spread" :class="{'descale-required': descaleRequired}">
        <div class="nav-left flex-start gutter-left">
            <!-- settings -->
            <div :class="{'nav-button flex':true, 'nav-button--stacked':isSearching}" @click="goToSettings">
                <i class="bcon-cog"></i>
            </div>
            <!-- filters -->
            <div :class="{'nav-button flex': true, 'nav-button--active':filtersOpened, 'nav-button--stacked':isSearching}" @click="openFilterPanel">
                <i class="bcon-sliders"></i>
            </div>
            <!-- search -->
            <div :class="{'nav-button flex searchbar': true, 'nav-button--active': isSearching}" @click="closeSearch">
                <input id="searchinput" type="text"  placeholder="  search recipe names"
                    :class="{'nav-searchbar':true, 'nav-searchbar--active':isSearching}" 
                    @click="openSearch($event)" @keyup="updateSearch"
                >
                <i class="bcon-search"></i>
            </div>
        </div>
        <div class="nav-right flex-end gutter-right">
            <!-- funnel rinse -->
            <div :class="{'nav-button flex':true, 'nav-button--stacked':isSearching}" @touchstart="startTap($event,'funnel')" @touchend="endTap">
                <i class="bcon-tint"></i>
            </div>
            <!-- chamber rinse -->
            <div :class="{'nav-button flex':true, 'nav-button--stacked':isSearching}" @touchstart="startTap($event,'chamber')" @touchend="endTap">
                <i class="bcon-tint"></i>
            </div>
            <!-- purge -->
            <div :class="{'nav-button flex':true, 'nav-button--stacked':isSearching}" @touchstart="startTap($event,'purge')" @touchend="endTap">
                <i class="bcon-coffee"></i>
            </div>
        </div>
    </div>
</template>

<script>
import appUpdate from '../scripts/eventBus.js'
import Store from '../store'
export default {
    created(){
        appUpdate.$on('toggleSearch',($event)=>{
            this.toggleSearch($event)
        })
        appUpdate.$on('filterPanel',($event)=>{
            this.filtersOpened = $event
        })

    },
    beforeDestroy(){
        appUpdate.$off("toggleSearch");
        appUpdate.$off('filterPanel');
    },
    computed:{
        isBLEConnected(){
            return Store.getters.isBLEConnected
        },
        funnelRinse(){
            let rec = Store.getters.getMaintenanceRecs.filter(rec=>{return rec.name.toLowerCase().trim() =="funnel rinse"})
            return rec[0];
       },
        chamberRinse(){
            let rec = Store.getters.getMaintenanceRecs.filter(rec=>{return rec.name.toLowerCase().trim() =="manual rinse"})
            return rec[0];
        },
        manualPurge(){
            let rec = Store.getters.getMaintenanceRecs.filter(rec=>{return rec.name.toLowerCase().trim() =="manual purge"})
            return rec[0];
       },
       
       descaleRequired(){
           return Store.getters.getDescaleRequired && this.isBLEConnected;
       }
    },
    methods:{
        startTap( e, button){
            // e.preventDefault();
            this.buttonPressed = button;
            clearInterval(this.tapTimer);
            const self = this;
            this.tapTimer = setInterval(()=>{
                self.pressTime += 1;
                if(self.pressTime > 6){
                    //start long cycle
                    clearInterval(self.tapTimer);
                    console.log(`Starting long ${self.buttonPressed} cycle`);
                }
            },50)
        },
        endTap(){
            if(this.pressTime <= 6){
                if(this.buttonPressed === 'funnel'){
                    console.log(`Starting normal funnel rinse cycle`);
                    //this.runRecipe(this.funnelRinse.id)
                    // this.$router.push(`/run/Funnel%20Rinse/Manual%20Cycle}`)
                }else if(this.buttonPressed === 'chamber'){
                    console.log(`Starting normal chamber rinse cycle`);
                    // this.$router.push(`/run/Chamber%20Rinse/Manual%20Cycle}`)
                }else if(this.buttonPressed === 'purge'){
                    console.log(`Starting normal manual purge cycle`);
                    // this.$router.push(`/run/Manual%20Dispense/Manual%20Cycle}`)
                }
            }else{
                //cancel the long press cycle
                console.log(`cancelling current long ${this.buttonPressed} cycle`)
            }
            clearInterval(this.tapTimer);
            this.buttonPressed = null;
            this.pressTime = 0;
        },
        runRecipe(id){
            Store.commit('updateRoute','/')
            // Store.dispatch('fetchRecipe', {"account":"0","id":id});
        },
        openSearch(e){
            e.stopPropagation();
            if(!this.isSearching){
                this.isSearching = true;
                this.searchTerm = '';
            }
        },
        closeSearch(e){
            e.stopPropagation();
            this.isSearching = false;
            this.searchTerm = '';
            // appUpdate.$emit('updateSearch', this.searchTerm)
        },
        updateSearch(){
            appUpdate.$emit('updateSearch', this.searchTerm)
        },
        openFilterPanel(){
            // this.filtersOpened = !this.filtersOpened;
            // appUpdate.$emit('filterPanel', this.filtersOpened)
        },
        goToMaintenance(){
            // window.vue.$router.push('/maintenance')
        },
        goToConnect(){
            // window.vue.$router.push('/connect')
        },
        goToSettings(){
            // window.vue.$router.push('/settings')
        }
    },
    data(){
        return{
            isSearching: false,
            searchTerm: '',
            filtersOpened: false,
            tapTimer: null,
            pressTime:0,
            buttonPressed:null
            
        }
    }
}
</script>



