<template>
    <div class="rec-story">
        <i class="bcon-times rec-modal-wrapper__story-close" @click="()=>$emit('close')"></i>
        <section class="story-title-section">
            <h3 class="rec-story-title">{{recipe.name}}</h3>   
            <div class="flex-start" style="width: 100%;"><i class="bcon-tag" style="margin-right:10px"></i><p class="flex rec-story-tags">{{recipe.tags.join(', ')}}</p></div>
        </section>
        <!-- Content -->
        <section v-for="(section, index) in  JSON.parse(recipe.text[1])" :key="index" 
            :class="{
                'rec-story-full__image flex': section.type == 1 && !!section.img,
                'rec-story-full': section.type == 1 && !!section.text,
                'rec-story-halves flex-spread': section.type == 2
                }" 
        >
            <!-- if full width -->
            <img v-if="section.type == 1 && !!section.img" class="full-width" :src="`${require('@/assets/recipes/'+section.img)}`">
            <p v-if="section.type == 1 && !!section.text">{{section.text}}</p>
            <!-- if 50/50 view -->
            <div v-if="section.type == 2" class="rec-story-left">
                <img v-if="!!section.content[0].img" class="full-width" :src="`${require('@/assets/recipes/'+section.content[0].img)}`">
                <p v-if="!!section.content[0].text">{{section.content[0].text}}</p>
            </div>
            <div v-if="section.type == 2" class="rec-story-right">
                <img v-if="!!section.content[1].img" class="full-width" :src="`${require('@/assets/recipes/'+section.content[1].img)}`">
                <p v-if="!!section.content[1].text">{{section.content[1].text}}</p>
            </div>
        </section>
    </div>
</template>

<script>
export default {
    props:{recipe:Object},
}
</script>



