<template>
    <div :style="'position:relative;width:'+this.width+'vw;height:'+this.width+'vw;'">
        <div class="bkonloader-track">
            <div class="bkonloader-dot-sm" :style="(color?'background:'+color:'' )"></div>
            <div class="bkonloader-dot" :style="(color?'background:'+color:'' )"></div>
        </div>
    </div>
</template>

<script>
export default {
    props:{width:Number, color:String}
}
</script>

<style>
.bkonloader-logo{
    position: absolute;
    width:40%;
    top: 40%;
    left: 30%;
    z-index: 100;
}
.bkonloader-track{
    width: 100%; 
    height:100%;
    animation: rotate 2.5s infinite ;
    opacity: 0;
    border-radius: 50%;
    position: relative;
    border: 1px solid transparent;
    z-index: 500;
}
 .bkonloader-dot{
    width: 60%; 
    height:60%;
    border-radius: 50%;
    background: #ed3721;
    position: absolute;
    left: 20%;
    top: 20%; 
    -webkit-box-shadow: 0px 0px 80px 12px rgba(0,0,0,.3);
    -moz-box-shadow: 0px 0px 80px 12px rgba(0,0,0,.3);
    box-shadow: 0px 0px 80px 12px rgba(0,0,0,.3);
 }
 .bkonloader-dot-sm{
    width: 25%; 
    height:25%;
    border-radius: 50%;
    background: #ed3721;
    position: absolute;
    top:0%; 
    z-index: 100;
    right: 0%;
    /* -webkit-box-shadow: 0px 0px 80px 12px rgba(0,0,0,.5);
    -moz-box-shadow: 0px 0px 80px 12px rgba(0,0,0,.5);
    box-shadow: 0px 0px 80px 12px rgba(0,0,0,.5); */
 }
 @keyframes rotate {

     0%{opacity: 0;transform:rotate(0deg);}
     5%{opacity: 1;transform:rotate(0deg);}
     20%{opacity: 1;transform:rotate(0deg);}
     50%{transform: rotate(360deg); opacity: 1;}
     65%{transform: rotate(360deg); opacity: 1;}
     70%{transform: rotate(360deg);opacity: 0;}
     80%{opacity: 0;}
     82%{transform: rotate(0);opacity: 0;}
 }
</style>



