import isConnectionValid from '../../../scripts/validateDevice'
const BLE = {
    state:{
        approvedDevices: window.localStorage.getItem('deviceList')? JSON.parse(window.localStorage.getItem('deviceList')):[],
        connectedDevice:null,
        isBLEConnected:false
    },
    actions:{
        setApprovedDevices(context, deviceList){
            console.log('setting approved devices: ',deviceList)
            if(window.localStorage.getItem('deviceList') != JSON.stringify(deviceList)){
                context.commit('setBLEPollTimeout', false)
            }
            context.commit('setApprovedDevices', deviceList)
        },
        clearDevices(context){
            context.commit('setApprovedDevices',[]);
            window.app.disconnectBLE();
            window.app.clearBLE();
        },
        setConnectedDevice(context, device){
            console.log('setting connected device: ', device)
            if(isConnectionValid(context.getters.getApprovedDevices, device)){
                context.commit('setConnectedDevice',device)
                context.commit('setUiId', device.ui_id);
                context.commit('setMachineId', device.machine_id);
                window.app.getDescale();
            }else{
                window.app.disconnectBLE();
            }
        }
    },
    mutations:{
        setApprovedDevices(state, deviceList){
            console.log('MUTATION: setting approved devices: ',deviceList)
            window.localStorage.setItem('deviceList', JSON.stringify(deviceList))
            state.approvedDevices = !!deviceList ? deviceList : [];
        },
        setConnectedDevice(state,device){
            console.log('setting connected device: ', device)
            state.connectedDevice = device;
        },
        setBLEConnected(state, bool){
            state.isBLEConnected = bool;
        }
    },
    getters:{
        getApprovedDevices(state){
            return state.approvedDevices;
        },
        isBLEConnected(state){
            return state.isBLEConnected;
        },
        getConnectedDevice(state){
            return state.connectedDevice;
        }
    }
}

export default BLE


