<template>
    <iframe 
        height="432" width="768" 
        :src="convertToEmbed(videos[currentVideo])" 
        frameborder="0" 
        allow=" autoplay; encrypted-media;" 
        allowfullscreen
    ></iframe>
</template>

<script>
export default {
    props:{videos:Array, currentVideo:Number},
    mounted(){
    },
    destroyed(){
        this.$emit('resetVideo')
    },
    methods:{
        convertToEmbed(url){
            return url.indexOf('autoplay') != -1 ? url.replace('watch?v=','embed/') : url.replace('watch?v=','embed/') + '?autoplay=1'
        }
    },
    data(){
        return {
        }
    }
}
</script>



