import appUpdate from './eventBus.js'
import Moment from './dateFormatter'
import Logger from './logger'
import ErrorCodes from '../../static/error-codes/errors-en.json'
import Store from '../store'
const dataUpdate = (data) => {
    switch (true) {
// USER
        case (data.indexOf('userDetails') != -1):
        let user = data.substring(12);
            if(user){
                Store.dispatch('setUser', JSON.parse(decodeURIComponent(user)))
            }else{
                window.vue.$router.push('/login')
            }
            window.app.isConnected()
            break;

        case (data.indexOf('logout:') !=-1):
            Store.dispatch('logout');
            break; 
        
        case (data.indexOf('approvedDevices:')!=-1):
            Store.dispatch('setApprovedDevices', JSON.parse(data.substring(16)))
            break;
        
        case (data.indexOf('this_mac:') !=-1):
            Store.commit('setMac', data.substring(9));
            break;


//BLE CONNECTION
        case (data.indexOf('new_device') != -1):
            appUpdate.$emit('devicesInRange', JSON.parse(data.substring(11)));       
            break;

        case (data.indexOf('is_connected') != -1):
            let connected = data.substring(13);
              Store.commit('setBLEConnected', parseInt(connected) === 1 ? true : false);
              if(parseInt(connected) === 0){
                  Store.dispatch('checkDescale', {recipes:0,operationHours:0,days:0,required:0});
                  Store.dispatch('clearFilters');
              }
            break;

        case (data.indexOf('connected_device:') != -1):
            let pairedDevice = JSON.parse(data.substring(17));
            Store.dispatch('setConnectedDevice', pairedDevice)
            let payload = {
                "account": Store.getters.getAccount, 
                "machine": pairedDevice.mac
            }
            window.app.getRecipes(JSON.stringify(payload))
            window.app.getRecipes(JSON.stringify({"account":0, "machine": pairedDevice.mac}));
            break;

//RECIPE DATA
        case(data.indexOf('recipes:') !=-1):
            console.log('recipes recieved');
            let arr = JSON.parse(data.substring(8))
            let recipes = arr.map(x=>{
                return JSON.parse(decodeURIComponent(x))
            })
            Store.dispatch('setRecipes', recipes);
            break;
        
        case(data.indexOf('systemRecs:') !=-1):
            let sysarr = JSON.parse(data.substring(11))
            let sysrecipes = sysarr.map(x=>{
                return JSON.parse(decodeURIComponent(x))
            })
            Store.commit('setSystemRecs', sysrecipes)
            break;

        case (data.indexOf('recipeReceived') != -1):
            let recipe = JSON.parse(decodeURIComponent(data.substring(15)));
            // console.log('from dataUpdate: ',recipe)
            if(parseInt(recipe.account_id) != 0){
                Store.dispatch('setCurrentRecipe', recipe)
            }else{
                Store.dispatch('setCurrentRecipe', recipe)
                setTimeout(()=>{
                    //start brewing maintenance recipe
                    // window.vue.$router.push({ name: 'Run', params: { name: recipe.name, size:'Maintenance' }})
                    // Store.dispatch('startBrewing', {steps:recipe.sequences, name: recipe.name, id: recipe.id, variation_name: 'Maintenance', account: recipe.account_id})
                    Store.dispatch('startBrewing', {
                        name: recipe.name, 
                        variation_id: recipe.id,
                        variation_name: "Maintenance", 
                        steps: recipe.sequences, 
                        id: recipe.id,
                        account: recipe.account_id, 
                        route: '/run/'+encodeURIComponent(recipe.name)+'/Maintenance'
                    })
                },500)
            }
            break;

        case (data.indexOf('favRecs:') != -1):
            Store.commit('setFavorites', data.substring(8))
            break;

        
        
//BREWING
        case (data.indexOf('stepCompleted') != -1):
            if(Store.getters.getIsBrewing && !!Store.getters.getBrewingRecipe && !!Store.getters.getBrewingRecipe.steps.length){
                appUpdate.$emit('stepCompleted');
            }
            break;

        case (data.indexOf('recipeCompleted') != -1):
            //Only execute if whole recipe was sent to BKC.
            if(Store.getters.getIsBrewing){
                appUpdate.$emit('recipeCompleted');
                //check for brewing recipe type
                if(Store.getters.getCurrentRecipe)
                Logger.Log({type: Store.getters.getBrewingRecipe.type, recipe_id: Store.getters.getBrewingRecipe.id, event: 200,
                    meta:{"name":encodeURIComponent(Store.getters.getBrewingRecipe.name),"size":encodeURIComponent(Store.getters.getBrewingRecipe.size), "action":"Completed"}
                })
            }
            break;

        case (data.indexOf('dialog') != -1):
            let dtext = decodeURIComponent(data.substring(7));
            appUpdate.$emit('dialog', dtext)
            console.log("Dialog sent from Data Updatet: ", dtext)
            break;

        case (data.indexOf('cialog') != -1):
            //dialog from inside step
            let ctext = data.substring(7);
            appUpdate.$emit('stepDialog', ctext)
            break;

        case (data.indexOf('error:') != -1):
            let msg = data.substring(6).split(":");
            let error;
            console.log("parsed error: ", msg)
            if(parseInt(msg[0]) == 9){
                error ={title:"Test Output",txt:msg[2],"event":44}
            }else{
                error = ErrorCodes.module[msg[0]].code[msg[2]]
            }
            let obj = {title: error.title, text: error.txt}
            if(msg[1].toLowerCase().indexOf('emergency') != -1){
                obj.fatal = true;
            }
            if(msg[1]){
                appUpdate.$emit('recipeError', obj)
                Logger.Log({event: error.event, type: 1, recipe_id: Store.getters.getBrewingRecipe.id ? Store.getters.getBrewingRecipe.id:null,
                    meta:{"module":msg[0],"code":msg[2],"name":encodeURIComponent(error.title)}
                })
                if(Store.getters.getBrewingRecipe.id){
                    //Log: Recipe brew interrupted by error
                    let act ="Error: C:"+msg[2]+":M:"+msg[0];
                    if(msg[0] == 9){
                        act = "Output: "+msg[2];
                    }
                    Logger.Log({event: 201, type: Store.getters.getBrewingRecipe.type, recipe_id: Store.getters.getBrewingRecipe.id,
                        meta:{"name":encodeURIComponent(Store.getters.getBrewingRecipe.name),"size":encodeURIComponent(Store.getters.getBrewingRecipe.size), "action":act}
                    })
                }
            }else{
                appUpdate.$emit('notify',("ERROR: "+data.substring(6)))
            }
            break;

//LOG DATA
        case (data.indexOf('incomingLog:')!= -1):
            let log = decodeURIComponent(data.substring(12));
            appUpdate.$emit('incomingLog', JSON.parse(log))
            break;
        
        case (data.indexOf('sendLogToWeb:')!= -1):
                if(!!Store.getters.getSyncNumber){
                    Store.dispatch('sendLogToWeb')
                }
                break;
        
 //System info 
        case (data.indexOf('sysCount')!= -1):
            let sys = JSON.parse(decodeURIComponent(data.substring(9)))
                appUpdate.$emit('sysCount',sys);
                break;

        case (data.indexOf('software_versions:')!= -1):
            let software = JSON.parse(data.substring(18))
            if(software.mb == "unknown" && software.ble == "unknown"){
                appUpdate.$emit('notify',("ATTENTION: The connected machine's Mainboard and BLE software may not be compatible with this device."))
            }else if(software.mb == "unknown" || software.ble == "unknown"){
                let ver = software.mb == "unknown" ? 'Mainboard' : 'BLE'
                appUpdate.$emit('notify',("ATTENTION: The connected machine's "+ver+" software may not be compatible with this device."))
            }
            appUpdate.$emit('software:',software);
            break;

        case (data.indexOf('notify:')!=-1):
            appUpdate.$emit('notify',data.substring(7));
            break;

        case (data.indexOf('checkDescale:') != -1):
            Store.dispatch('checkDescale', JSON.parse(data.substring(13)))
            break
// DEFAULT            
        default:
        console.log('DATA UPDATE DID NOT FIT CASE: ', data)
            break;
    }
}

export default dataUpdate


