<template>
    <div :class="{'added-to-fav': !isOpened,'flex':true, 'added-to-fav__show':isOpened,'iphonex-favchip':isiPhoneX}">
        <p class="added-to-fav-text">
            added to favorites 
        </p>
        <i class="bcon-star added-to-fav-text-i"></i>
    </div>
</template>

<script>
import Store from '../../store'
export default {
    props:{isOpened: Boolean},
    methods:{
        close(){
            this.$emit('close')
        }
    },
    computed:{
        isiPhoneX(){
           return Store.getters.isiPhoneX;
        }
    }
}
</script>

<style>
    .added-to-fav{
        position: absolute;
        bottom: 15vw;
        left: 30vw;
        opacity: 0;
        pointer-events: none;
        width: 40vw;
        height: 12vw;
        background: gray;
        border-radius: 20vw;
        -webkit-box-shadow: 0px 0px 80px 12px rgba(0,0,0,.5);
        -moz-box-shadow: 0px 0px 80px 12px rgba(0,0,0,.5);
        box-shadow: 0px 0px 80px 12px rgba(0,0,0,.5);
        z-index: 4000;
        transition: .5s;
        pointer-events: none;
    }
    .added-to-fav__show{
        position: absolute;
        bottom: 15vw;
        left: 30vw;
        opacity: 1;
        pointer-events: none;
        width: 40vw;
        height: 12vw;
        background: #3d3e39;
        border-radius: 20vw;
        -webkit-box-shadow: 0px 0px 80px 12px rgba(0,0,0,.5);
        -moz-box-shadow: 0px 0px 80px 12px rgba(0,0,0,.5);
        box-shadow: 0px 0px 80px 12px rgba(0,0,0,.5);
        z-index: 4000;
        transition: .3s;
        pointer-events: inherit;
    }
    .added-to-fav-text{
        font-size: 3vw;
        font-weight: 700;
    }
    .added-to-fav-text-i{
        color: #8a7c0d;
        margin-left:1vw; 
        font-size: 3vw;
    }
</style>



