<script>
export default {
    props:{step:Object},
    destroyed(){
        this.volume = 0;
        this.fillSpeed = 0;
    },
    watch:{
        step(newStep, oldStep){
            console.log('current step', newStep)
            this.handleVolume(newStep);
        }
    },
    methods:{
        handleVolume(step){
            switch (true) {
                case step.type.toLowerCase() === 'fi':
                    let fill = step.values.v ? parseInt(step.values.v) : 0;
                    this.volume += fill;
                    this.fillSpeed = Math.round(fill/17); 
                    break;

                case step.type.toLowerCase() === 'ri':
                    let rinse = step.values.v ? parseInt(step.values.v) : 0;
                    this.volume += rinse;
                    this.fillSpeed = Math.round(rinse/17);
                    break

                case step.type.toLowerCase() === 'pg':
                    this.volume = 0;
                    this.fillSpeed = 7;
                    break;
                default:
                    break;
            }
        },
        decodeStep(step){
            console.log(step)
            switch (true) {
                case step.type.toLowerCase().indexOf('st') != -1:
                    let start = step.values.v ? parseInt(step.values.v) : 0;
                    return `Heating to ${start}˚F`;
                    break
                case step.type.toLowerCase() === 'fi':
                    let fill = step.values.v ? parseInt(step.values.v) : 0;
                    return `Filling ${fill}ml`;
                    break;
                case step.type.toLowerCase() === 'ri':
                    let rinse = step.values.v ? parseInt(step.values.v) : 0;
                    return `Rinsing ${rinse}ml`;
                    break

                case step.type.toLowerCase() === 'sp':
                    let steep = step.values.v ? parseInt(step.values.v) : 0;
                    return `Steeping ${steep} ${steep == 1? 'second':'seconds'}`;
                    break

                case step.type.toLowerCase() === 'vc':
                    let vac = step.values.v ? parseInt(step.values.v) : 0;
                    return `Vacuum ${vac}kPh`;
                    break

                case step.type.toLowerCase() === 'pg':
                    return `Dispensing`;
                    break;
                
                case step.type.toLowerCase() === 'dialog':
                    return `Dialog`;
                    break;

                case step.type.toLowerCase() === 'bo':
                    return 'Finishing recipe';
                    break;

                default:
                    break;
            }
        }
    },
    computed:{
        isFilling(){
            return this.step.type.toLowerCase() === 'ri' || this.step.type.toLowerCase() === 'fi' || this.step.type.toLowerCase() === 'rf'; 
        }
    },
    data(){
        return {
            volume: 0,
            fillSpeed: 0,
            currentStep: ''
        }
    }
}
</script>

