            setTimeout(function(){window.app.isConnected = function(){return true};console.log('window: connect to :', mac); window.vue.$children[0].dataUpdate('is_connected:1')},1000)
            setTimeout(()=>{window.vue.$children[0].dataUpdate('connected_device:{"mac":"123456789","name":"Jessie Test"}')},1050)
