            window.vue.$children[0].dataUpdate("sysCount:%7B%22cip%22%3A0,%22mbVersion%22%3A%22unknown%22,%22aborts%22%3A0,%22bleVersion%22%3A%22unknown%22,%22manPurge%22%3A0,%22recipes%22%3A45,%22uiVersion%22%3A%221.0%22,%22descale%22%3A0,%22manRinse%22%3A0%7D")
