<template>
    <div class="run-step-track-wrapper">
        <div class="run-step-track" :style="`transform:translate3d(-${115*currentStep}px,0,0)`">
            <div class="run-step" style="visibility: hidden"></div>
            <div class="run-step" :class="{'active':currentStep === i}"
                v-for="(step, i) in steps" :key="'seq '+i" 
                v-show="step.type.toLowerCase() != 'bo'"
            >
                <div class="flex" style="height:100%; width: 100%;">
                    <i v-if="step.type.toLowerCase().indexOf('st') != -1 || step.type.toLowerCase() == 'pg'" :class="'bcon-'+decodeStep(step)"></i>
                    <img v-else-if="step.type.toLowerCase() == 'fi'" class="run-step-fill" src="../assets/steps/fill.png" alt="">
                    <img v-else-if="step.type.toLowerCase() == 'ri'" class="run-step-rinse" src="../assets/steps/rinse.png" alt="">
                    <img v-else-if="step.type.toLowerCase() == 'sp'" class="run-step-steep" src="../assets/steps/steep.png" alt="">
                    <img v-else-if="step.type.toLowerCase() == 'vc'" class="run-step-vacuum" src="../assets/steps/vacuum.png" alt="">
                    <img v-else-if="step.type.toLowerCase() == 'dialog'" class="run-step-dialog" src="../assets/steps/dialog.png" alt="">
                </div>
            </div>
            <div class="run-step" :class="{'active':steps[currentStep].type.toLowerCase() == 'bo'}"
               
            >
                <div class="flex" style="height:100%;width: 100%;">
                    <i class="fas fa-flag-checkered"></i>
                    <!-- <i :class="'bcon-' + decodeStep(step)"></i> -->
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props:{steps: Array, currentStep:Number},
    computed:{
        stepNames(){
            let stepNames = []
            let $this = this;
            this.steps.forEach(step => {
                stepNames.push($this.decodeStep(step))
            });
            return stepNames
        }
        },
    methods:{
        currentStepName(i){
            let step =this.steps[i];
            return step.type.toLowerCase().indexOf('st') != -1 || step.type.toLowerCase() == 'pg'?
            'fill'
            :this.decodeStep(step);
        },
        decodeStep(step){
            switch (true) {
                case step.type.toLowerCase().indexOf('st') != -1:
                    return 'thermometer-fill';
                    break;
                case step.type.toLowerCase() === 'fi':
                    return 'fill';
                    break;
                case step.type.toLowerCase() === 'ri':
                    return 'rinse';
                    break;
                case step.type.toLowerCase() === 'sp':
                    return 'steep';
                    break;
                case step.type.toLowerCase() === 'vc':
                    return 'vacuum';
                    break;
                case step.type.toLowerCase() === 'pg':
                    return 'chamber-dispense';
                    break;
                case step.type.toLowerCase() === 'dialog':
                    return 'dialog';
                    break;
            
                default:
                    break;
            }
        }
    }
}
</script>

<style>

</style>



