<template>
    <div :class="{'dialog-wrapper': true, 'dialog-wrapper-opened': isOpened, 'flex': true}">
            <div class="dialog-box gutter">
                <!-- <img class="dialog-dots" src="../../assets/bkon-dots.png" alt=""> -->
                <div class="flex">
                <p>{{dialogText}}</p>
                </div>
            <div class="flex" v-if="type == 'step'">
            <button class="dialog-dismiss" type="button" name="button" @click="closeDialog(0)">Stop</button>
            </div>
            <div v-else class="flex">
                <button class="dialog-dismiss" type="button" name="button" @click="closeDialog(0)">Abort</button>
                <button class="dialog-dismiss" type="button" name="button" @click="closeDialog(1)">Continue</button>
            </div>
        </div>
  </div>
</template>

<script>
export default {
    props:{isOpened:Boolean, type: String, dialogText:String},
    methods:{
        closeDialog(input){
            this.$emit('closeDialog', input)
        }
    }
}
</script>

<style>

</style>



