import appUpdate from '../../../scripts/eventBus'
import dataUpdate from '../../../scripts/dataUpdate'
import Axios from 'axios'
import { stat } from 'fs';

const WEB = {
    state:{
        baseURL: 'http://52.42.41.9/craftcloud/frontend/web',
        isSyncing: null,
        error:null,
        requestBLEPoll:null,
        isUpdate: false
    },
    actions:{
//USER API CALLS
        login(context, creds, rootState){
            if(!navigator.onLine){
                dataUpdate('notify:No internet connection detected.');
                return
            }
            context.commit('setIsSyncing', 'login');
            let req = context.getters.getBaseURL + "/account/auth";
            console.log('login path: ',req)
            window.localStorage.setItem('username', creds.user)
            Axios.post(req, {"username":creds.user, "password":creds.password})
            .then(function (response) {
                console.log("response: ", response.data)
                if(response.data.sync_number.length != 0 && response.data.account_id.length != 0){
                console.log('sync response: ', response.data);
                window.app.saveUser(JSON.stringify({"creds":response.data, "username": creds.user}));
                context.dispatch('setUser', {creds:response.data, username: creds.user},{root:true})
                }else{
                appUpdate.$emit('loginError', "the username and password did not match");
                console.log('sync didnt match length check: ', response.data);
                }
                context.commit('setIsSyncing', null);
            })
            .catch(function (error) {
                console.log(error);
                context.commit('setIsSyncing', null);
                appUpdate.$emit('loginError', error);
            });
        },
//BLE API CALLS
        getDeviceListFromWeb(context, iterator){
            console.log("trying to get approved devices")
            if(!navigator.onLine){
                dataUpdate('notify:No internet connection detected. Turn on to ensure your approved devices list is accurate.');
                return
            }
            context.commit('setIsSyncing', 'devices');
            let req = context.getters.getBaseURL + "/machine/access";
            let myUUID = context.getters.getMac;
            let userID = context.getters.getUserId;
            Axios.post(req,{uuid:myUUID,user_id:userID}, {headers:{"SYNC": context.getters.getSyncNumber}})
            .then(function (response) {
                console.log("approved devices: ", response.data);
                context.dispatch('setApprovedDevices', response.data)
                context.commit('setIsSyncing', null);
                // context.commit('setBLEPollTimeout', false);
            })
            .catch(function (error) {
                console.log(error);
                if(iterator){
                    context.commit('setIsSyncing', null);
                    appUpdate.$emit('Error getting devices: ', error);
                }else{
                    context.dispatch('getDeviceListFromWeb', 1);
                }
            });
        },
        requestBLEApproval(context,device){
            if(!navigator.onLine){
                dataUpdate('notify:No internet connection detected. Could not send your request to Craft Cloud.');
                return
            }
            context.commit('setIsSyncing', 'bleApproval');
            let req = context.getters.getBaseURL + "/machine/request";
            let myUUID = context.getters.getMac;
            let userID = context.getters.getUserId;
            console.log('sending request to ',req)
            Axios.post(req,{user_id:userID,uuid:myUUID,mac:device.mac,name:device.name}, {headers:{"SYNC": context.getters.getSyncNumber}})
            .then((response)=>{
                appUpdate.$emit('bleApprovalRequest',{status:'sent'})
                context.commit('setIsSyncing', null);
                if(context.getters.requestBLEPoll === null){
                    context.commit('setBLEPollTimeout', setInterval(()=>{
                        context.dispatch('getDeviceListFromWeb');
                        console.log("Polling for approved devices.")
                    },5000))
                }
            })
            .catch(function (error) {
                console.log(error);
                context.commit('setIsSyncing', null);
                appUpdate.$emit('bleApprovalRequest', {status:'error',error:error});
            });
        },

//RECIPE API CALLS
        getRecipesFromWeb(context){
            if(!navigator.onLine){
                dataUpdate('notify:No internet connection detected. Could not get recipes from Craft Cloud.');
                context.commit('setIsUpdate', false);
                return
            }
            context.commit('setIsSyncing', 'recipes');
            let req = context.getters.getBaseURL + '/recipe/';
            // let req = context.getters.getBaseURL + '/machine/recipes/' + machine_id;
            Axios.get(req,{headers:{"SYNC": context.getters.getSyncNumber}})
            .then(function (response) {
                let res = response.data;
                // console.log('response :\n', res);
                if(res.metas.length != 0) {
                    let stringifyEachRec = [];
                    const account = context.getters.getAccount;
                    const machine = context.getters.getConnectedDevice.mac;
                    if(context.getters.getIsUpdate){
                        //if updating, delete current recipes first
                        console.log('deleting recipes from inside ajax request')
                        window.app.deleteAllRecipes(
                            JSON.stringify({"account": account,"machine":machine})
                        )
                        setTimeout(()=>{
                            res.metas.forEach(rec=>{
                                stringifyEachRec.push(JSON.stringify(rec))
                            })
                            window.app.saveRecipes(JSON.stringify({
                                "account": account,
                                "machine": machine,
                                "recipes": JSON.stringify(stringifyEachRec)
                            }))
                            context.commit('setIsSyncing', 'updating')
                        }, 1000)
                    }else{
                        //no reciepes for this machine, save recipes
                        res.metas.forEach(rec=>{
                            stringifyEachRec.push(JSON.stringify(rec))
                        })
                        window.app.saveRecipes(JSON.stringify({
                            "account": account,
                            "machine": machine,
                            "recipes": JSON.stringify(stringifyEachRec)
                        }))
                        context.commit('setIsSyncing', null)
                    }
                }else{
                    window.app.saveRecipes(JSON.stringify({
                        "account": context.getters.getAccount,
                        "machine": context.getters.getConnectedDevice.mac,
                        "recipes": "[]"
                    }))
                    context.commit('setIsSyncing', null);
                    context.commit('setIsUpdate', false);
                    
                    dataUpdate('notify:No recipes are assigned to this machine on the Craft Cloud.')
                    console.log('res start:\n',res,'\nres was empty');
                }
                setTimeout(()=>{
                    context.dispatch('getMaintenanceRecipesFromWeb')
                },500)
            })
            .catch(function (error) {
                console.log('Error requesting recipes: \n', error);
                context.commit('setIsSyncing', null);
                context.commit('setIsUpdate', false);
                dataUpdate('notify:There was an error requesting your recipes. Please try again.');
                // self.isError = true;
            });
        },
        getMaintenanceRecipesFromWeb(context){
            if(!navigator.onLine){
                dataUpdate('notify:No internet connection detected. Could not get maintenance recipes from Craft Cloud.');
                context.commit('setIsUpdate', false);
                return
            }
            console.log("requesting system recipes from web")
            context.commit('setIsSyncing', 'maintenance');
            let req = context.getters.getBaseURL + '/recipe/maintenance';
            Axios.get(req,{headers:{"SYNC": context.getters.getSyncNumber}})
            .then(function (response) {
                let res = response.data;
                console.log('response :\n', res);
                if(res.length != 0) {
                    let stringifyEachSysRec = [];
                    res.forEach(rec=>{
                        stringifyEachSysRec.push(JSON.stringify(rec))
                    })
                    window.app.saveRecipes(JSON.stringify({
                        "account": 0,
                        "machine": context.getters.getConnectedDevice.mac,
                        "recipes": JSON.stringify(stringifyEachSysRec)
                    }))
                }else{
                    window.app.saveRecipes(JSON.stringify({
                        "account": 0,
                        "machine": context.getters.getConnectedDevice.mac,
                        "recipes": "[]"
                    }))
                context.commit('setIsSyncing', null);
                console.log('res was empty');
                }
                context.commit('setIsSyncing', null);
            })
            .catch(function (error) {
                console.log('Error requesting maintenance recipes: \n', error);
                context.commit('setIsUpdate', false);
                dataUpdate('notify:There was an error requesting your maintenance recipes. Please try again.')
                context.commit('setIsSyncing', null);
                // self.isError = true;
            });
        },
//LOG API CALLS
        sendLogToWeb(context){
            let creds = {"sync": context.getters.getSyncNumber,
                "url":context.getters.getBaseURL+"/data-log",
                "ui_id":1}
            window.app.sendLogToWeb(JSON.stringify(creds))
        }
    },
    mutations:{
        setIsSyncing(state, whatIsSyncing){
            state.isSyncing = whatIsSyncing
        },
        setBLEPollTimeout(state, interval){
            if(state.requestBLEPoll === null){
                state.requestBLEPoll = interval;
            }else{
                clearInterval(state.requestBLEPoll);
                state.requestBLEPoll = null;
            }
        },
        setIsUpdate(state, bool){
            state.isUpdate = bool;
            console.log('isUpdate set to '+bool)
        }
    },
    getters:{
        getRequestOffset(state){
            return state.requestOffset;
        },
        getIsSyncing(state){
            return state.isSyncing;
        },
        getBaseURL(state){
            return state.baseURL;
        },
        requestBLEPoll(state){
            return state.requestBLEPoll;
        },
        getIsUpdate(state){
            return state.isUpdate;
        }
    }
}

export default WEB


