<template>
    <div class="recipes" :class="{'ios-scroll':!filtersPanel}">
        <!-- Not connected -->
        <div v-if="!isBLEConnected"  class="recipes-empty flex" @click="openConnectPage">
            <div v-if="isSyncing === 'recipes'" class="flex">
                <Loader :width="7" color="white" id="rec-loader"/>
            </div>
            <div v-else>
                <!-- <i class="bcon-brewer-slashed recipes-empty-icon " id="rec-cloud"></i> -->
                <p class="recipes-empty-text">Not connected to a machine</p>
                <p class="recipes-empty-sub-text">Tap to Connect</p>
            </div>
        </div>
        <!-- no recipes, ble is connected ,and not syncing-->
        <div v-else-if="recipeCount === 0 && isBLEConnected && !isSyncing"  class="recipes-empty flex" @click="getRecipesFromWeb">
            <div v-if="isSyncing === 'recipes'" class="flex">
                <Loader :width="7" color="white" id="rec-loader"/>
            </div>
            <div v-else>
                <p class="recipes-empty-text">There are no recipes on this device. </p>
                <p class="recipes-empty-sub-text">Tap to download from Craft Cloud</p>
                <i class="bcon-cloud-down recipes-empty-icon" id="rec-cloud"></i>
            </div>
        </div>
        <div v-else>
            <div v-if="isUpdate || isSyncing == 'updating' || isSyncing === 'recipes'" class="flex recipes-empty">
                <Loader :width="7" color="white" id="rec-loader"/>
            </div>
            <div v-else-if="recipeCount != 0 && recipes.length === 0"  class="recipes-empty flex" @click="getRecipesFromWeb">
                <div><i class="bcon-frown recipes-empty-icon"></i>
                    <p class="recipes-empty-text">No recipes fit this filter</p>
                </div>
                
            </div>
             <!-- Else Display recipes -->
            <transition v-else
                name="page-transition" 
                enter-active-class="animated fadeInDown" 
                leave-active-class="animated fadeOutDown" 
                mode="out-in" style="height:100%"
            >
               <!-- 
                   **************
                   recipe display
                   **************                 
                -->
                <recipe-list 
                    v-if="currentView === 1" 
                    :recipes="recipes" :tags="tags"
                    @handleSelection="selectRecipe($event)"
                    :isModalOpened="isModalOpened"
                    :viewVersion="currentView"
                />
                <vertical-view 
                    v-if="currentView === 2||currentView === 3" 
                    :recipes="recipes" :tags="tags"
                    @handleSelection="selectRecipe($event)"
                    :isModalOpened="isModalOpened"
                    :viewVersion="currentView"
                />
                <horizontal-view 
                    v-if="currentView === 4 ||currentView === 5" 
                    :recipes="recipes" :tags="tags"
                    @handleSelection="selectRecipe($event)"
                    :isModalOpened="isModalOpened"
                    :viewVersion="currentView"
                />
            </transition>
            <!-- Added to Favorites Chip -->
            <FavoritesChip :isOpened="showChip" @close="favsChip(false)"/>
        </div>
        <!-- Filter Side Panel -->
        <FilterPanel 
        :isOpened="filtersPanel" 
        :tags="tags" 
        :currentView="currentView"
        @switchView="switchView($event)"
        />
        <!-- Chips  -->
        <div v-if="(currentFilters.length > 0 || currentCollection != '') && isBLEConnected"  
            class="recipes-applied-collection-wrapper" 
            @click="togglePanel(true)"
        >
            <div v-if="currentFilters.length > 0" 
                class="recipes-applied-collection flex recipes-applied-collection-tag"
                v-for="(tag, index) in currentFilters" :key="index"
            >
                <i class="bcon-tag"></i>
                {{tag}}
            </div>
            <div v-if="currentCollection != ''" 
                :class="'recipes-applied-collection flex ' +hoverFilterClass(currentCollection) " 
            >
                <i class="bcon-sliders"></i>
                {{currentCollection.replace("$","")}}
            </div>
        </div>

        <!-- Single Recipe Selection Modal -->
        <recipe-modal v-if="isModalOpened" :view="modalView" @close="killRecSelection"/>
        <i v-if="isVideoOpened" class="bcon-times overlay-video-close" @click="setIsVideoOpened(false)"></i>
        <div :class="{'overlay-visible-panel':filtersPanel, 'overlay':!filtersPanel}" @click="togglePanel(false)"></div>
        <div :class="{'overlay-visible':isModalOpened, 'overlay':!isModalOpened, 'overlay-video':isVideoOpened}" @click="closeAll($event)"></div>

        <!-- Secret Button -->
        <div @click="secretButton" style="height:80px;width:80px;position:fixed;left:0;bottom:0;z-index:5500;"></div>
    </div>
</template>

<script>
import FilterPanel from './Filter-Panel.vue'
import RecipeList from './recipeviews/RecipeList.vue'
// import SearchView from './recipeviews/SearchView.vue'
import VerticalView from './recipeviews/VerticalView.vue'
import HorizontalView from './recipeviews/HorizontalView.vue'
import RecipeModal from './SingleRecipeModal.vue'
import FavoritesChip from './popups/Chip.vue'
import Loader from './BkonLoader.vue'
import AppUpdate from '../scripts/eventBus.js'
import Store from '../store'
export default {
    components:{
        FilterPanel,
        HorizontalView,
        RecipeList,
        VerticalView,
        RecipeModal,
        //SearchView,Favorites,
        FavoritesChip,Loader},
    created(){
        AppUpdate.$on('filterPanel', ($event)=>{
            this.filtersPanel = $event;
        }),
        AppUpdate.$on('updateSearch',($event)=>{
            this.searchTerm = $event;
        }),
        AppUpdate.$on('addedFav', ()=>{
            this.favsChip(true)
        }),
        AppUpdate.$on('footerDownloadClicked',()=>{
            this.getRecipesFromWeb()
        })
    },
    beforeDestroy(){
        AppUpdate.$off("filterPanel");
        AppUpdate.$off("updateSearch");
        AppUpdate.$off("addedFav");
        AppUpdate.$off("footerDownloadClicked");
    },
    methods:{
        
        secretButton(){
            if(this.isModalOpened){
                this.modalView=!this.modalView;
            }else{
             this.currentView === 5? this.currentView = 1 : this.currentView +=1;
            }
            window.localStorage.setItem('recView', this.currentView)
        },
        selectRecipe(id){
            this.selectedRec = id
            this.isModalOpened = true;
            Store.dispatch('fetchRecipe',{"account":this.account,"id":id})
        },
        setIsVideoOpened(isVideoOpened){
            Store.commit('setIsVideoOpened', isVideoOpened);
        },
        killRecSelection(){
            this.isModalOpened = false; 
            this.selectedRecipe=null;
            Store.dispatch('setCurrentRecipe', {sequences:[{name:''}]});
        },
        filterClass(type){
                return 'filter-chip__' + type;
        },
        togglePanel(bool){
            AppUpdate.$emit('filterPanel',bool)
        },
        closeAll(e){
            e.stopPropagation();
            if(this.isVideoOpened){return}
            this.isModalOpened = false;
            this.selectedRec = null;
            //clear recipe in store
        },
        hoverFilterClass(type){
            if(type=="tea" || type=="coffee" || type=="infusion" || type=="spirit" || type=="other"){
                return 'filter-chip__active-' + type;
            }else{
                return 'filter-chip__active-user';
            }
        },
        favsChip(bool){
            this.showChip = bool;
            let self =this
            setTimeout(()=>{
                self.showChip = false;
            },1500)
        },
        openConnectPage(){
            this.$router.push('/connect')
        },
        getRecipesFromWeb(){
            Store.dispatch('getRecipesFromWeb');
            
        }
    },
    computed:{
        recipes(){
            let recsInView = [];
            let allRecs = Store.getters.getRecipes;
            //filter by collection
            if(this.currentCollection == ''){
                recsInView = allRecs;
            }else{
                let filteredRecs = [];;
                let self= this;
                allRecs.forEach(rec=>{
                    let match = false
                    if(rec.tags.indexOf(self.currentCollection) != -1){ match = true  }
                    if(self.decodeRecipeType[rec.type] == self.currentCollection){match = true}
                    if(match){filteredRecs.push(rec)}
                })
                recsInView = filteredRecs;
            }
            //then filter by tags
            if(this.currentFilters.length == 0){
                return recsInView;
            }else{
                let filteredRecs2 = [];
                let filters = this.currentFilters;
                let self= this;
                recsInView.forEach(rec=>{
                    let match = false
                    rec.tags.forEach(tag=>{
                        if (filters.indexOf(tag) != -1){ match = true  }
                    })
                    if(match){filteredRecs2.push(rec)}
                })
                recsInView = filteredRecs2;
            }
            return recsInView;
        },
        tags(){
            return Store.getters.tags;
        },
        recipeCount(){
            return Store.getters.getRecipeCount
        },
        account(){
            return Store.getters.getAccount
        },
        searchedRecipes(){
            let self = this;
            let arr =[]
            this.recipes.forEach(rec =>{
                if (rec.name.toLowerCase().indexOf(self.searchTerm.toLowerCase().trim()) != -1){
                    arr.push(rec)
                }
            })
            return arr
            // this.recipes.filter(rec => {
            //     // console.log('recipe: \n', rec,"\nSearch Term:\n", self.searchTerm )
            //     return rec.name.toLowerCase().indexOf(self.searchTerm.toLowerCase().trim()) != -1;
            // })
        },
        currentLayout(){
            return Store.getters.getCurrentLayout;
        },
        isSyncing(){
            return Store.getters.getIsSyncing;
        },
        isBLEConnected(){
            return Store.getters.isBLEConnected;
        },
        isUpdate(){
            return Store.getters.getIsUpdate;
        },
        currentCollection(){
            return Store.getters.getCurrentCollection;
        },
        currentFilters(){
            return Store.getters.getCurrentFilters;
        },
        isVideoOpened(){
           return Store.getters.isVideoOpened;
       }
    },
    data(){
        return{
            selectedRecipe: null,
            filtersPanel: false,
            isModalOpened: false,
            showChip: false,
            searchTerm: '',
            currentView: window.localStorage.getItem('recView') ? parseInt(window.localStorage.getItem('recView')) : 4,
            modalView: false,
            decodeRecipeType:{
                "1":"tea","2":"coffee","3":"spirit","4":"infusion","5":"other"
            }
        }
    }
}
</script>



