<template>
    <section class="animated slideInRight confirm" ref="confirm" @click="(e)=>e.stopPropagation()">
        <div>
        <section :class="{'confirm-main-wrapper':true}">
            <i class="bcon-chevron-left confirm-back gutter" @click="goback"></i>
            <section class="gutter-lr" style="padding-top: 72px;">
                <h3 class="confirm-title">{{portion.name}}</h3>   
                <div class="flex-start" style="width: 100%; opacity:.7;">
                    <p class="flex confirm-subtitle">{{parentRecipeName}}</p>
                </div>
                <div class="rec-modal-divider" style="margin-top: 14px"></div>
            </section>

            <section class="flex-spread gutter-lr">
                <div :class="{'flex confirm-section-title':true, 'rec-nutrition-sections--active': activeView == 1}" @click="setView(1)">Preparation</div>
                <div :class="{'flex confirm-section-title':true, 'rec-nutrition-sections--active': activeView == 2}" @click="setView(2)">Execution</div>
            </section>
            <section class="confirm-ingredients-wrapper gutter-lr" v-show="activeView == 1">
                <!-- <h3 class="rec-modal-title-sm">Preparation</h3> -->
                <!-- <div class="rec-modal-divider"></div> -->
                <!-- ingredient list -->
                <p  v-if="portion.ingredients.length === 0" class="rec-ingredient-list-empty gutter-tb" style="text-align:left:opacity: .7;">There are no additional ingredients to prepare.</p>
                <ul v-else class="rec-ingredient-list confirmlist">
                    <li v-for="(item, i) in portion.ingredients" :key="i" class="flex-spread">
                        <div>
                            <p>{{item.name}}</p>
                            <span><i>{{item.text}}</i></span>
                        </div>
                        <div class="rec-ingredient-list-value">
                            {{item.value}}
                        </div>
                    </li>
                </ul>
                <div class="rec-modal-divider"></div>
            </section>
            <section class="confirm-ingredients-wrapper gutter-lr" v-show="activeView == 2">
                <div class="flex-spread gutter-tb" style="opacity: .7; border-top:1px solid rgba(0,0,0,.1)">
                    <div class="flex-start rec-ingredient-list-value">
                        <i class="bcon-stopwatch" style="padding-right: 10px;"></i>
                        <span >{{getRuntime(portion.sequences)}}</span>
                    </div>
                    <p class="rec-ingredient-list-value">{{portion.sequences.length}} steps</p>
                </div>
                <div class="rec-step-list">
                    <div v-show="showSteps" v-for="(step, i) in portion.sequences" :key="i" class="rec-steps-card flex-spread ">
                        <div class="rec-steps-title flex-start">
                            <div class="rec-steps-icon flex">
                                <i v-if="step.type.toLowerCase() == 'st' || step.type.toLowerCase() == 'pg'" :class="'bcon-'+decodeStep(step)[0]"></i>
                                <img v-else
                                :class="decodeStep(step)[0]"
                                 :src="require(`../../assets/steps/${decodeStep(step)[0]}.png`)" alt="">
                            </div>
                            <div>
                                <h2>{{decodeStep(step)[1]}}</h2>
                                <div class="flex-start" style="opacity:.7;font-size:18px;">
                                    <i class="bcon-stopwatch" style="margin-left:30px ;padding-right: 10px;"></i>
                                    <p>{{getRuntime([step])}}</p>
                                </div>
                            </div>
                        </div>
                            <p class="rec-steps-title-time">{{decodeStep(step)[2]}}</p>
                    </div>
                    <div class="rec-modal-divider"></div>
                    <!-- show/hide button -->
                    <div class="rec-ingredient-list-toggle flex-spread" @click="showSteps = !showSteps">
                        <h3 class="rec-modal-title">Execution Preview</h3>
                        <button class="flex gutter-tb">
                            <i class="bcon-chevron-right" :style="showSteps?'transform:rotate(-90deg);transition:.2s;': 'transition:.2s'"></i>
                        </button>
                    </div>
                    <div class="rec-modal-divider"></div>
                </div>
            </section>
        </section>
        <!-- start button -->
        <section class="confirm-start-button-wrapper gutter" :class="{'fixed':!(activeView == 2 && showSteps)}">
            <button class="confirm-start-button flex" @click="setBrewRecipe(portion)">
                Start
            </button>
        </section>
        </div>
    </section>
</template>

<script>
import Store from '../../store'
import RunTime from '../../scripts/runtime'
export default {
    props: {portionIndex: Number, recipe:Object, parentRecipeName:String},
    computed:{
        portion(){
            return this.portionIndex != null ? 
            this.recipe.recipes[this.portionIndex] : 
            {sequences:[]}
        }
    },
    methods:{
        setBrewRecipe(portion){
            Store.dispatch('startBrewing', {
                name: this.parentRecipeName,
                variation_id: portion.id,
                variation_name: portion.name, 
                steps: portion.sequences, 
                id: this.recipe.id,
                route: `/run/${encodeURIComponent(this.parentRecipeName)}/${encodeURIComponent(portion.name)}`
            })
        },
        setView(view){
            this.activeView = view;
            window.localStorage.setItem('confirmView', view);
        },
        DecodeStepName(name){
            return DecodeStepName(name);
        },
        getRuntime(steps){
            let length = RunTime(steps).reduce((total,time)=>total + time);
            let minutes = parseInt(length / 60, 10);
            let seconds = parseInt(length % 60, 10);
            //add 0 to make 09,08,etc
            seconds = seconds < 10 ? "0" + seconds : seconds;
            return minutes + ':' + seconds;
        },
        decodeStep(step){
            switch(!!step){
                case step.type.toLowerCase().indexOf('st') != -1:
                    return ['thermometer-fill', 'Heat Up', step.values.v + '˚F']
                    break
                case step.type.toLowerCase().indexOf('fr') != -1 || step.type.toLowerCase().indexOf('fi') != -1 :
                //fill
                    return ['fill', 'Fill', step.values.v + 'ml']
                    break
                case step.type.toLowerCase().indexOf('rf') != -1:
                //chamber rinse
                    return ['rinse', 'Funnel Rinse', step.values.v + 'ml']
                    break

                case step.type.toLowerCase().indexOf('ri') != -1:
                //funnel rinse
                    return ['rinse', 'Rinse', step.values.v + 'ml']
                    break

                case step.type.toLowerCase().indexOf('sp') != -1:
                //steep
                    return ['steep', 'Steep', step.values.v + (step.values.v == 1? ' sec':' secs')]
                    break

                case step.type.toLowerCase().indexOf('vc') != -1:
                //vacuum
                    return ['vacuum', 'Vacuum', step.values.v + 'kPa']
                    break
                
                case step.type.toLowerCase().indexOf('pg') != -1:
                //vacuum
                    return ['chamber-dispense', 'Dispense', step.values.ml + 'oz']
                    break
                
                case step.type.toLowerCase().indexOf('di') != -1:
                //dialog
                    return ['dialog', 'Dialog', step.values.text]
                    break
                //make one for blowout
                default:
                    return ['','','']
            }
        },
        goback(){
            let $this = this;
            this.$refs.confirm.classList.add('slideOutRight')
            setTimeout(()=>$this.$emit('back')
            ,100)
        },
    },
    data(){
        return {
            showAll: false,
            showAllSteps: false,
            showSteps: false,
            activeView: window.localStorage.getItem('confirmView') ? parseInt(window.localStorage.getItem('confirmView')) : 1
        }
    }
}
</script>



