import Vue from 'vue'
import Vuex from 'vuex'
import Axios from 'axios'
import VueAxios from 'vue-axios'
import App from './App'
import router from './router'
import './styles/main.scss'
import Store from './store'
import recipes from './recipes.js'
import windowApp from './window-app'
// import checkDevice from './scripts/checkDevice'

Vue.config.productionTip = false

Vue.use(Vuex);
Vue.use(VueAxios, Axios);
/* eslint-disable no-new */
window.vue = new Vue({
  el: '#app',
  router,
  components: { App },
  Store,
  Axios,
  template: '<App/>',
  mounted(){
    windowApp()
    // Store.commit('setRecipes', recipes)
    // Store.commit('setSystemRecs', [{name:'manual rinse',id:'12',tags:['maintenance']},{name:'manual purge',id:'12',tags:['maintenance']}])
    // const device = checkDevice();
    // if(device == 'iPhoneX' || device == 'iPhoneXsMax' || device == 'iPhoneXr'){
    //   Store.commit('setiPhoneX', true)
    // }
    Store.dispatch('initUser')

  }
})



