<template>
    <section :class="{'gutter-tb': view, 'gutter-bottom':!view}">
        <h3 v-if="view" class="rec-modal-title gutter-bottom gutter-lr">Portions</h3>
        <div v-if="view" class="rec-modal-divider"></div>
        <div 
            class="rec-portion" 
            v-for="(portion, i) in portions" :key="i"
            @click="$emit('select', i)"
        >
            <section class="rec-portion-section flex-spread">
                <div class="rec-portion-section-title-container">
                    <h4 class="rec-portion-section-title">{{portion.name}}</h4>
                    <div class="flex-start rec-portion-stats">
                        <i class="bcon-stopwatch"></i>
                        <span>{{getRuntime(portion.sequences)}}</span>
                        <i class="bcon-thermometer-fill"></i>
                        <span>{{getTemp(portion.sequences)}}</span>
                    </div>
                </div>
                <i class="bcon-chevron-right" style="opacity: .3;"></i>
            </section>
            <div :style="i == portions.length-1? 'opacity: 0;':''" class="rec-modal-divider"></div>
        </div>
        <div class="rec-modal-divider"></div>
    </section>
</template>

<script>
import RunTime from '../../scripts/runtime'
export default {
props:{portions:Array, view:Boolean},
methods:{
    getRuntime(steps){
        let length = RunTime(steps).reduce((total,time)=>total + time);
        let minutes = parseInt(length / 60, 10);
        let seconds = parseInt(length % 60, 10);
        //add 0 to make 09,08,etc
        seconds = seconds < 10 ? "0" + seconds : seconds;
        return minutes + ':' + seconds;
    },
    getTemp(steps){
        for(var i=0; i<steps.length; i++){
            if(steps[i].type.toLowerCase().indexOf('st') != -1){
                return `${!!steps[i].values.v ? steps[i].values.v : steps[i].values.tmp} ˚F`;
            }
        }
        return '?'
    }
}
}
</script>



