<template>
    <div class="filter-tags"
        @touchstart="startDrag($event)"
        @touchmove="drag($event)" 
        @touchend="endDrag"
    >
        <div class="filter-header">
            <!-- Top bar -->
            <div class="panel-bar flex">
                <i class="bcon-tag"></i>
                <p>Tags</p>
            </div>
            <!-- search bar -->
            <div class="filter-search-bg flex">
                <input v-if="isOpened" class="filter-search" type="text" v-model="searchTerm">
                <i v-show="this.searchTerm != ''" class="bcon-close" @click="clearSearch"></i>
            </div>
            <!-- Active Filters -->
            <p class="block-title">Active Tags</p>
            <div class="filter-selected-wrapper flex">
                <div v-if="selectedFilters.length === 0" class="filter-selected-empty">No tags selected</div>
                <div v-else class="filter-chip filter-chip__active filter-chip__active-tag flex" v-for="filter in reversedFilters" :key="filter +'-selected'"
                    @click="selectFilter(filter)"
                >
                    <div class="flex">
                        <i class="bcon-tag" style="margin-right:1vw"></i>
                        <p style="padding-right:1vw;">{{filter.replace("$","")}}</p>
                        <i class="bcon-times-circle"></i>
                    </div>
                </div>
                <div v-if="selectedFilters.length != 0" class="filter-chip filter-chip-spacer"></div>
            </div>  

            <div class="filter-divider"></div>
        </div>
        
        <!-- Filter list -->
        <div class="filter-body" v-if="searchTerm === ''">
            <div 
                class="filter-chip filter-chip__tag flex-start" 
                :class="{'filter-chip__active-tag':selectedFilters.indexOf(tag) !=-1}"
                v-for="(tag, index) in tags" :key="'panel'+tag+index"
                @click="selectFilter(tag)"
            >
            <i class="bcon-tag" style="display:inline;margin-right: 1vw;"></i>
                <p style="display:inline">{{tag}}</p>
            </div>
        </div>
        <div class="filter-header" v-else>
            <div v-if="searchedFilters.length == 0" class="filter-selected-empty">
                no tags match search :(
            </div>
            <div v-else class="filter-chip" v-for="(tag,index) in searchedTags" :key="tag + index"
                :class="filterClass(tag)" @click="selectFilter(tag)"
            >
                <p>{{tag}}</p>
            </div>
        </div>
    </div>
</template>

<script>
import Store from '../../store'
export default {
    props:{
        isOpened:Boolean,
        selectedFilters: Array,
        dragDistance:Number,
        tags:Array
    },
    computed:{
        collections(){
            return Store.getters.getCollections;
        },
        searchedTags(){
            let self = this;
            let search = this.searchTerm.trim().toLowerCase();
            let filterSearch = this.collections.filter(filt => filt.toLowerCase().indexOf(search) != -1);
            return this.typeFilters.filter(filt => filt.toLowerCase().indexOf(search) != -1).concat(filterSearch)
        },
        reversedFilters(){
            return this.selectedFilters.slice().reverse();
        },
        panelTitleHeight(){
            return document.getElementById('tag-panel-top').scrollHeight;
        }
    },
    methods:{
        filterClass(type){
            //returns a class to style the collection chips
            if(this.selectedFilters.indexOf(type) != -1){
                return 'filter-chip__active filter-chip__active-' + type;
            }else{
                return 'filter-chip__' + type;
            }
        },
        selectFilter(filter){
            this.$emit('selectFilter',filter);
            this.searchTerm = '';
        },
        clearSearch(){
            this.searchTerm = '';
        },
         startDrag(e){
            this.dragX = e.touches[0].clientX;
        },
        drag(e){
            let distance = e.touches[0].clientX - this.dragX
            if(distance > 0){
                const pos = distance < 350? distance : 350;
                this.$emit('updatePosition', pos-350);
            }
        },
        endDrag(){
            if(this.dragDistance+350 > 50){
                // console.log('finish dragright')
                this.$emit('updatePosition', 0);
                // this.$emit('switchView');
            }else{
                this.$emit('updatePosition', -350);
            }
            this.dragX = 0;
        },
    },
    data(){
        return{
            searchTerm: '',
            dragX: 0,
            typeFilters: ['tea','coffee','spirit','infusion','other']
        }
    }
}
</script>

<style>

</style>



