<template>
  <div id="app" class="root">
    <div :class="{'content':true, 'content-descale-warning':descaleRequired}">
      <transition 
        name="page-transition" 
        enter-active-class="animated fadeIn" 
        leave-active-class="animated fadeOut" 
        mode="out-in"
      >
        <router-view/>
      </transition>
    </div>
    
    <!-- <Notification/> -->
    <!-- <PanelLeft :isOpened="panelLeft" @closePanel="closePanel"/> -->
    <!-- <div :class="{'overlay-visible':overlayOpened, 'overlay':!overlayOpened}" @click="closePanel"></div> -->
  </div>
</template>

<script>
// import PanelLeft from './pages/Panel-Left.vue'
import Notification from './components/popups/Notification.vue'
import AppUpdate from './scripts/eventBus.js'
import DataUpdate from './scripts/dataUpdate.js'
import Store from './store/'
export default {
  name: 'App',
  components:{Notification},
  computed:{
    overlayOpened(){
      return  this.panelLeft;
    },
    descaleRequired(){
      return Store.getters.getDescaleRequired && Store.getters.isBLEConnected;
    }
  },
  methods:{
    dataUpdate(data){
      console.log('data update received: ', data)
      DataUpdate(data)
    }
  },
  data(){
    return{
    }
  }
}
</script>



